@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: block;
    color: #000;
    background-color: #fff;
    border-radius: 10px;

    &.disabled {
        opacity: .4;
        filter: grayscale(1);
    }

    > .thumbnail {
        width: 100%;
        padding-top: 100%;
        position: relative;
        background-color: #efefef;
        overflow: hidden;

        > img {
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            transform: translate3d(0,0,0);
        }

        .emptyPlaceholder {
            width: 100%;
            height: 100%;
            background-color: #eee;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 10px;
        }
    }

    .meta {
        padding: 8px;
    }

    .title {
        display: block;
        margin: 0;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        color: #000;
        @include crop();
    }

    .price {
        display: block;
        font-weight: normal;
        font-size: 12px;
        line-height: 12px;
        color: #8e8e8e;
        margin: 5px 0 0 0;
        @include crop();

        .discountPrice {
            font-weight: bold;
            color: #000;
            margin: 0 8px 0 0;
        }

        .lineThrough {
            text-decoration: line-through;
        }
    }
}
