@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: relative;
    background-color: #fff;
    z-index: 1;
    height: 56px;

    @media all and (max-width: $mobile-breakpoint) {
        height: 41px;
    }
}

.toggle {
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 16px 16px 0px 16px;
    background-color: #fff;
    z-index: 2;

    @media all and (max-width: $mobile-breakpoint) {
        padding-top: 0;
    }
}

.label {
    font-weight: bold;
    font-size: 28px;
    line-height: 41px;
    margin: 0;
    flex: 1;
    @include crop();

    > span {
        font-weight: normal;
    }
}

.menu {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    margin: 0;
    list-style: none;
    padding: 0;
    background-color: #fff;
    z-index: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    max-height: calc(100vh - 56px);

    &.visible {
        transform: none;
    }
}
