@import '@mssgme/scss-basics/mixins/clearfix';
@import '@mssgme/scss-basics/mixins/crop';
@import '@mssgme/scss-basics/vars';

.heading {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 6px 0;
    //padding: 0 0 0 28px;

    > img {
        display: block;
        width: 20px;
        height: auto;
        position: absolute;
        top: 0;
        left: 0;
    }
}

.bio {
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 30px 0;

    > p {
        font-size: 14px;
        line-height: 17px;
        color: rgba(0, 0, 0, 0.6);
        margin: 0 0 15px 0;
    }

    .userLink {
        display: block;
        position: relative;
        background-color: #f5f5f5;
        border-radius: 4px;
        overflow: hidden;

        @include clearfix();

        > i {
            display: block;
            float: left;
            width: calc(100% - 135px);
            font-style: normal;
            padding: 11px 16px;
            text-align: left;
            @include crop();

            &::after {
                content: attr(data-success);
                display: flex;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                justify-content: center;
                align-items: center;
                background-color: #3eb991;
                font-weight: 500;
                color: #fff;
                opacity: 0;
                pointer-events: none;
                transform: scale(0.9);
                transition: transform 0.3s ease, opacity 0.3s ease;
            }

            &.copied::after {
                opacity: 1;
                transform: scale(1);
                pointer-events: auto;
            }
        }

        i::selection {
            background: rgba(0, 0, 0, 0);
        }

        > span {
            width: 135px;
            max-width: 100%;
            display: block;
            padding: 11px 16px;
            float: right;
            font-weight: 500;
            color: #007aff;
            cursor: pointer;
            user-select: none;
            text-align: right;
        }
    }

    .tip {
        border-radius: 4px;
        background-color: rgba(0, 122, 255, 0.05);
        padding: 10px 16px;
        margin: 6px 0 0 0;
        font-size: 14px;
        line-height: 20px;

        > p {
            margin: 0;
        }
    }
}

.shareList {
    list-style: none;
    margin: 0 auto;
    width: 70%;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;

    > li {
        > a {
            font-size: 21px;
            color: white;
            border-radius: 40px;
            text-align: center;
            padding: 10px 4px 6px 10px;
            display: inline-block;

            > svg {
                margin: -1px 6px 0 0;
            }

            &.twitter {
                background-color: #2aa3ef;
            }

            &.facebook {
                background-color: #3a589b;
            }

            &.vkontakte {
                background-color: #4a7298;
            }

            &.instagram {
                background: radial-gradient(
                    circle at 30% 107%,
                    #fdf497 0%,
                    #fdf497 5%,
                    #fd5949 45%,
                    #d6249f 60%,
                    #285aeb 90%
                );
            }
        }
    }
}

.widgetSection {
    text-align: center;
}

.section {
    margin: 0 0 15px 0;
}

.label {
    font-size: 16px;
    font-weight: normal;
    color: #8e8e93;
    line-height: 22px;
    display: block;
    margin: 0 0 10px 0;

    &.sectionHeading {
        margin: 0 0 16px 0;
    }
}

.codeBlock {
    overflow: hidden;
    position: relative;
    padding: 11px 16px;
    border-radius: 4px;
    background-color: #f5f5f5;
    font-size: 16px;
    line-height: 22px;
    word-break: break-all;
    cursor: pointer;
    text-align: left;

    &::after {
        content: attr(data-success);
        display: flex;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        background-color: #3eb991;
        font-weight: 500;
        color: #fff;
        opacity: 0;
        pointer-events: none;
        transform: scale(0.9);
        transition: transform 0.3s ease, opacity 0.3s ease;
    }

    &.copied::after {
        opacity: 1;
        transform: scale(1);
        pointer-events: auto;
    }
}

.codeBlock::selection {
    background: rgba(0, 0, 0, 0);
}

.tipCode {
    font-size: 13px;
    line-height: 18px;
    margin: 16px 0 0 0;
    white-space: pre-line;
    text-align: left;
}

.sectionBlock {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    padding: 16px;
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.16);
    margin-bottom: 16px;
    border-radius: 10px;
}
.headingTitle {
    font-size: 20px;
    color: #000;
    text-align: left;
    font-weight: 500;
    display: inline-block;
    margin-left: 12px;
    margin-bottom: 12px;
    margin-top: 0px;
}
.headingInstruction {
    display: block;
    line-height: 17px;
    text-align: left;
    font-size: 14px;
    color: #8e8e8e;
    margin: 0px;
    font-weight: 400;
}

