.instagramTipImg {
    display: block;
    width: 280px;
    max-width: 100%;
    margin: 22px auto;
    border-radius: 4px;
}

.instagramSettingsLink {
    text-align: center;
    font-size: 14px;
    line-height: 17px;
}

.socialsTab {
    text-align: center;
}
.chevronRight {
    position: absolute;
    right: 16px;
    top: 36px;
    fill: black;
}
.mainIcon {
    position: relative;
    max-width: 20px;
    max-height: 20px;
    top: 1px;
}
.Badge {
    position: relative;
    top: -4px;
    margin-left: 6px;
}
