.root {
    border-radius: 10px;
    margin-left: -20px;
    margin-top: -40px;
    margin-bottom: -30px;
    width: calc(100% + 40px);
    box-sizing: border-box;
    text-align: center;
    font-size: 18px;
    font-weight: 500;

    p {
        margin-top: 24px;
        margin-bottom: 24px;
    }

    a {
        display: inline-block;
        text-decoration: none;
        color: white;
        background: #007aff;
        border-radius: 30px;
        padding: 16px;
        width: 90%;
        margin-bottom: 30px;
    }
}

.header {
    background: #2eb67c;
    color: white;
    border-radius: 10px 10px 0 0;
    padding-top: 16px;
    padding-bottom: 16px;
}
