.root {
}

.priceContainer {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 16px 0;

    .priceInput {
        flex: 1;
        max-width: calc(100% - 105px);
    }

    .currencySelect {
        margin: 0 0 0 16px;
    }
}

.publishedContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 0 10px 0;

    &.priceTogglerContainer {
        margin: 0 0 16px 0;
    }
}

.publishedLabel {
    display: block;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    padding: 0 0 0 16px;
    position: relative;

    &.priceLabel {
        padding: 0;
    }
}
