.root {
    width: 100%;
    margin: 8px 0 0 0;
    padding: 16px;
    background-color: #fff;
    transition-delay: 0.4s !important;

    &.slideDown {
        transform: translateY(100px);
        opacity: 0;
    }

    &.animated {
        opacity: 1;
        transform: translateY(0);
        transition: transform 1s ease, opacity 1s ease;
    }
}
