.tabs {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
}

.tab {
    display: block;
    color: #333;
    font-size: 14px;
    font-weight: normal;
    line-height: 18px;
    cursor: pointer;
    padding: 0 6px;
    border-bottom: 2px solid transparent;

    > span {
        display: block;
        background-color: #f7f7fa;
        padding: 8px 16px;
        border-radius: 100px;
        transition: background-color 0.3s ease, color 0.3s ease;
    }

    &.active > span {
        background-color: #007aff;
        color: #fff;
    }
}
