@import '@mssgme/scss-basics/vars';

.root {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 999;

    &Legacy {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.5);
}

.sheet {
    position: absolute;
    bottom: 16px;
    left: 16px;
    width: var(--modal-width);
    max-height: var(--modal-max-height);
    display: flex;
    flex-direction: column;

    &Legacy {
        position: relative;
        max-width: 600px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        bottom: auto;
        left: auto;
    }
}

.fullWidth {
    width: 100%;
    left: 0;
    bottom: 0;

    .sheetPane {
        border-radius: 16px 16px 0 0;
    }
}

.fullHeight {
    --modal-padding-top: var(--safe-area-inset-top);

    height: 100% !important;
    max-height: 100%;

    .sheetPane {
        border-radius: 0;
    }

    .hasHeader {
        --modal-padding-top: 0px;
        --modal-header-padding-top: var(--safe-area-inset-top);
        --modal-header-height: calc(var(--modal-header-padding-top) + var(--modal-header-inner-height));
    }
}

.sheetPane {
    border-radius: 10px;
    background-color: var(--modal-bg);
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;

    &.inverse {
        --modal-bg: var(--color-light-gray);
    }
}

.sheetyPane {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;
    padding-top: var(--modal-padding-top);
    padding-bottom: var(--modal-padding-bottom);
}

.header {
    flex-shrink: 0;
}

.padded {
    padding: 16px;
}

.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
}

.content::-webkit-scrollbar {
    width: 6px;
}

.content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

.content::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 3px;
    transition: background 0.3s ease;
}
