.root {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 0 12px 0;
}

.heading {
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #333;
    margin: 0;
}

.counterBlock {
    display: flex;
    flex-wrap: wrap;
}

.counter {
    position: relative;
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    margin: 0 0 0 8px;
    padding: 0 0 0 12px;
}

.counter.green {
    color: #3eb990;
}

.counter.green::before {
    content: '';
    display: block;
    box-sizing: border-box;
    width: 8px;
    height: 6px;
    border-style: solid;
    border-width: 0 4px 6px 4px;
    border-color: transparent transparent #3eb990 transparent;
    position: absolute;
    left: 0;
    top: calc(50% - 4px);
}

.counter.red {
    color: #ec565d;
}

.counter.red::before {
    content: '';
    display: block;
    box-sizing: border-box;
    width: 8px;
    height: 6px;
    border-style: solid;
    border-width: 6px 4px 0 4px;
    border-color: #ec565d transparent transparent transparent;
    position: absolute;
    left: 0;
    top: calc(50% - 4px);
}
