@import 'button';

.button {
    @include button;
    padding: 10px 20px;
    border-radius: 100px;
    background-color: #f2f2f2;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #333;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.05);
    transition: box-shadow 0.3s ease, transform 0.3s ease;

    &:hover {
        box-shadow: 0 10px 10px -6px rgba(0, 0, 0, 0.14);
        transform: translateY(-2px);
    }
}

.startIcon {
    @include startIcon;
}

.endIcon {
    @include endIcon;
}
