.root {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    background-color: #fff;
}

.content {
    width: 100%;
    height: calc(100% - 50px);
    overflow: hidden;
}

.iframe {
    width: 100%;
    height: 100%;
}

.headerBtns {
    display: flex;
    align-items: center;
}

.headerLink {
    display: flex;
    align-items: center;
    height: 34px;
    margin: 0 12px 0 0;
}

.headerLink:last-child {
    margin: 0 6px 0 0;
}
