.modal {
    width: calc(100% - 32px);
    border-radius: 10px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 16px;
    z-index: 15;
    opacity: 0;
    pointer-events: none;
    transition: transform 0.3s ease, opacity 0.3s ease;
    padding: 16px;

    &.visible {
        opacity: 1;
        pointer-events: auto;
        transform: translateY(-50%);
        transition: transform 0.6s ease, opacity 0.6s ease;
    }
}

.title {
    display: block;
    font-size: 15px;
    line-height: 25px;
    font-weight: 500;
    text-align: center;
}

.overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    z-index: 15;
    pointer-events: none;
    transition: opacity 0.3s ease;

    &.visible {
        opacity: 1;
        pointer-events: auto;
        z-index: 10;
    }
}
