@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
    padding: 0 9px;
    position: relative;
    z-index: 10;
    background: #fff;

    > h2 {
        width: calc(100% - 155px);
        font-weight: 500;
        font-size: 16px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        margin: 0;
        text-align: center;
        @include crop();
    }

    > h2 + button:only-of-type {
        margin-left: auto;
    }
}
