.freeBanner {
    background: #f2f2f2;
    border-radius: 8px;
    width: 90%;
    margin: 0 auto;
    text-align: center;
    padding: 12px;
    margin-bottom: 20px;

    p {
        margin: 0;
    }

    h3 {
        margin-top: 0;
        margin-bottom: 8px;

        span {
            color: white;
            background: black;
            padding: 2px 4px;
            border-radius: 4px;
            margin-left: 4px;
        }
    }

    span {
        color: black;
        padding: 6px 12px;
        border-radius: 16px;
        border: 1px solid black;
        margin-top: 12px;
        display: inline-block;
    }
    .trial {
        span {
            background: #f33e34;
            border: 1px solid #f33e34;
        }
    }
    .btn {
        color: white;
        background: #007aff;
        border: 1px solid #007aff;
    }
}
