@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-shrink: 0;
    flex-grow: 0;
    left: 0;
    top: 0;
    bottom: 0;
    width: 61px;
    background: #fafafa;

    &.hidden {
        display: none;
    }

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
    }
}

.header {
    display: none;
    padding: 6px 8px;
    background-color: #fff;

    @media all and (max-width: $mobile-breakpoint) {
        display: block;
    }
}

.nav {
    padding: 4px 7px;
    border-right: 1px solid #F2F2F2;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media all and (max-width: $mobile-breakpoint) {
        position: absolute;
        left: 0;
        top: 0;
        width: 300px;
        max-width: calc(100% - 75px);
        height: 100%;
        background-color: #fafafa;
        transform: translateX(-100%);
        transition: transform .6s ease, box-shadow .6s ease;
        z-index: 100;
        padding: 16px;

        &.visible {
            box-shadow: 4px 0 16px rgba(0,0,0,.08);
            transform: none;
        }
    }
}

.logo {
    display: flex;
    width: 100%;
    height: 44px;
    margin: 8px 0 16px 0;
    align-items: center;
    justify-content: center;

    svg {
        display: block;
        width: 28px;
        height: auto;
        color: #000;
        position: relative;
        left: 3px;
    }

    @media all and (max-width: $mobile-breakpoint) {
        svg {
            width: 36px;
        }
    }
}

.menuBtn {
    width: 40px;
    height: 40px;
    color: #000;
    position: relative;
    z-index: 1;

    &.marked::after {
        content: '';
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #F14941;
        position: absolute;
        top: 8px;
        right: 6px;
        border: 2px solid #fff;
    }
}

.menuIcon {
    display: block;
    width: 24px;
    height: auto;
}
