@import '@mssgme/scss-basics/mixins/crop';
@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    align-items: center;
    max-width: 300px;
    // min-width: 300px;
    margin: 0;

    @media all and (max-width: $mobile-breakpoint) {
        cursor: pointer;
    }
}

.caption {
    padding: 0 20px 0 10px;
    flex: 1;
    min-width: 0;
}

.contactName {
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    margin: 0 0 2px 0;
    @include crop();
}
