.root {
    display: flex;
    width: 100%;
    height: 36px;
    align-items: center;
    justify-content: center;
    color: #fff;
    border-radius: 10px;
    background-color: #2f3542;
    user-select: none;
    overflow: hidden;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    font-weight: 500;
    font-size: 14px;
    line-height: 32px;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 0.02px;

    @media all and (min-width: 1025px) {
        &:hover {
            box-shadow: 0 10px 10px -6px rgba(0, 0, 0, 0.24);
            transform: translateY(-2px);
        }
    }

    > svg {
        fill: currentColor;
        display: inline-block;
        width: 21px;
        height: auto;
        vertical-align: top;
        margin: 0;
        // transform: scale(1.2);
        pointer-events: none;
    }

    > span {
        display: inline-block;
        vertical-align: top;
        margin: 0 0 0 8px;
        pointer-events: none;
    }
}

.messenger {
    background-image: linear-gradient(to bottom, #00b1ff, #006dfe);

    > span::before {
        content: 'Messenger';
    }
}

.whatsapp {
    background-image: linear-gradient(to bottom, #60fd7c, #07b825);

    > span::before {
        content: 'WhatsApp';
    }
}

.telegram {
