@import '@mssgme/scss-basics/vars';

.root {
    font-size: 14px;
    line-height: 20px;
    text-align: center;

    p:last-child {
        margin-bottom: 0;
    }
}

.header {
    margin: 0 0 10px 0;
}

.title {
    width: 100%;
    font-weight: bold;
}

.icon {
    color: $font-default-color;
    display: block;
    width: 10px;
    height: 10px;
    transition: transform 0.5s ease;
}

.iconBtn {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;

    &:hover {
        .icon {
            transform: rotate(90deg);
        }
    }
}
