.header {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    margin-bottom: 20px;

    > p {
        margin: 0;
        font-size: 14px;
        line-height: 20px;
        color: #8e8e93;
    }
}

.navButton {
    $color: #000;
    display: block;
    font-size: 16px;
    color: $color;
    padding: 10px;
    text-decoration: none;

    svg {
        display: block;
    }

    &:hover,
    &:focus {
        color: lighten($color, 20%);
    }

    &.disabled {
        opacity: 0.2;
        pointer-events: none;
    }
}
