@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    font-size: 14px;
    line-height: 20px;
}

.navContainer {
    padding: 5px 16px 0 16px;
}

.personalLinkContainer {
    padding: 14px 16px 16px 16px;

    @media all and (max-width: $mobile-breakpoint) {
        padding-top: 0;
    }

    > h2 {
        font-weight: bold;
        font-size: 28px;
        line-height: 41px;
        margin: 0 0 12px 0;
    }

    > .userLinkContainer {
        display: flex;
        align-items: center;
        width: 100%;
        height: 36px;
        padding: 0 0 0 16px;
        background: #f2f2f2;
        border-radius: 10px;
        position: relative;
        overflow: hidden;

        > .userLink {
            @include crop();
            display: inline-block;
            vertical-align: top;
            max-width: calc(100% - 64px);
            font-weight: normal;
            font-size: 16px;
            color: #000;
            padding: 0 6px 0 0;
            margin-right: auto;
            transition: color 0.3s ease;

            &:hover {
                color: $link-color;
            }
        }
    }
}

.bottomShare {
    width: 100%;
    padding: 0 0 24px 0;
    // background-image: linear-gradient(to bottom, transparent, #fff 80%);
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    justify-content: center;
}

.shadowed {
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
}

.topNav {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 16px 0 0 0;
}

.topNavItem {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: #000;
    cursor: pointer;
    margin: 0 10px;
    min-width: 18%;

    &:hover > .topNavItemIcon > svg,
    &:hover > .topNavItemIcon > img {
        transform: scale(0.8);
    }

    &:hover > .topNavItemLabel {
        color: $link-color;
    }

    &.topNavItemEn {
        margin: 0 18px;
    }

    &.copied {
        .topNavItemIcon {
            background: #2eb67c;
        }
    }
}

.topNavItemIcon {
    width: 54px;
    height: 54px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f2f2f2;
    transition: background 0.3s ease;

    > svg,
    > img {
        display: block;
        transition: transform 0.3s ease;
    }
}

.topNavItemLabel {
    display: block;
    margin: 8px 0 0 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    transition: color 0.3s ease;
}

.checkIcon {
    animation: revealCheck 0.3s ease forwards;
}

@keyframes revealCheck {
    0% {
        opacity: 0;
        transform: scale(0.3);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.elToSelect {
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
}

.previewBtn {
    display: flex;
    height: 100%;
    width: 44px;
    justify-content: center;
    align-items: center;
    background: #eaeaea;
}
