@import '@mssgme/scss-basics/mixins/crop';

.root {
    padding: 11px 0 30px 0;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.2);
    position: relative;
    margin: 0 0 14px 0;

    &:last-of-type {
        margin: 0;
    }
}

.total {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 4px 0;
}

.button {
    display: block;
    position: absolute;
    bottom: 10px;
    right: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #007aff;
    cursor: pointer;
    padding: 0 12px 0 0;

    &::after {
        content: '';
        display: block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px 3px 0 3px;
        border-color: #007bff transparent transparent transparent;
        position: absolute;
        right: 0;
        top: 7px;
    }

    &.expanded::after {
        transform: rotate(180deg);
    }
}

.list {
    font-size: 14px;
    line-height: 20px;
    margin: 0 0 6px 0;
    padding: 0;
    list-style: none;

    > li {
        display: flex;
        justify-content: space-between;
        box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
        padding: 11px 0 8px 0;

        &:last-of-type {
            box-shadow: none;
        }

        > span:first-of-type {
            display: block;
            flex: 1;
            @include crop();
            padding: 0 10px 0 0;
        }
    }
}
