@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: relative;
    width: calc(100% - 32px);
    padding: 16px 14px 14px 14px;
    background-color: #fff;
    transition-delay: 0.2s !important;
    margin: -46px auto 0 auto;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    border-radius: 10px;

    &.slideDown {
        transform: translateY(100px);
        opacity: 0;
    }

    &.animated {
        opacity: 1;
        transform: translateY(0);
        transition: transform 1s ease, opacity 1s ease;
    }
}

.userInfoContainer {
    display: flex;
    padding: 0 2px;

    .userPhoto {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .userNameCategory {
        display: flex;
        width: calc(100% - 60px);
        padding: 0 0 0 16px;
        align-items: center;

        > div {
            max-width: 100%;
        }

        h1 {
            width: 100%;
            font-weight: bold;
            font-size: 24px;
            line-height: 28px;
            color: #333;
            margin: 0;
            word-break: break-word;
            // @include crop();
        }

        p {
            width: 100%;
            font-size: 12px;
            line-height: 14px;
            color: #8e8e8e;
            margin: 2px 0 0 0;
            @include crop();
        }
    }
}

.description {
    margin: 12px 0 14px 0;
    padding: 0 2px;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    word-break: break-word;

    p {
        margin: 0;
        white-space: pre-wrap;
    }
}

.expandBtn {
    cursor: pointer;
    font-weight: 500;
    margin: 0 0 0 3px;
}
