@import '@mssgme/scss-basics/mixins/clearfix';

.root {
    display: flex;
    margin-bottom: 2px;

    &:last-child {
        margin-bottom: 0;
    }
}

.reverse {
    justify-content: flex-end;
}

.message {
    position: relative;
    font-size: 16px;
    line-height: 22px;
    border-radius: 3px 15px 15px 3px;
    padding: 5px 12px 6px 12px;
    word-break: break-word;

    .default & {
        color: #f2f2f2;
        background-color: #333;
    }

    .secondary & {
        color: #333;
        background-color: #f2f2f2;
    }

    .warning & {
        color: #2b303a;
        background-color: #fef7e4;
        border: 1px solid #e8e8e8;
    }

    .root:not(:last-child):first-child & {
        border-top-left-radius: 15px;
    }

    .root:not(:first-child):last-child & {
        border-bottom-left-radius: 15px;
    }

    .root:only-child & {
        border-radius: 15px;
    }

    .reverse & {
        border-radius: 15px 3px 3px 15px;
    }

    .reverse:not(:last-child):first-child & {
        border-top-right-radius: 15px;
    }

    .reverse:not(:first-child):last-child & {
        border-bottom-right-radius: 15px;
    }
}

.timestamp {
    font-size: 11px;
    line-height: 11px;
    color: #aaa;
    display: block;
    margin: 4px 0 0 0;

    .reverse & {
        text-align: right;
    }
}
