.root {
    width: 100%;
    padding: 16px;
    border-radius: 16px;
    background-color: #f5f5f5;
    text-align: left;
    color: #777;
    line-height: 1.4;

    p {
        margin: 0 0 10px 0;
    }

    > *:last-child {
        margin: 0;
    }
}

.title {
    display: block;
    width: 100%;
    font-weight: 500;
    letter-spacing: .4px;
    margin: 0 0 10px 0;
    color: #fff;

    > span {
        display: inline-block;
        padding: 3px 9px;
        border-radius: 8px;
        background-color: #aaa;
    }
}
