@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    position: relative;
    background-color: $background-light;
    border-radius: 10px;
    overflow: hidden;
    text-align: left;
    padding: 0 16px;
    gap: 8px;
    column-gap: 8px;
}

.tag {
    display: block;
    flex: 1;
    font-style: normal;
    padding: 8px 0;
    text-align: left;
    width: 90%;
    width: min-content;
    @include crop();

    &::after {
        content: attr(data-success);
        display: flex;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        position: absolute;
        top: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        background-color: $success;
        font-weight: 500;
        color: $font-light-color;
        opacity: 0;
        pointer-events: none;
        transform: scale(1.2);
        transition: transform $default-transition, opacity $default-transition;
    }

    &.copied::after {
        opacity: 1;
        transform: scale(1);
        pointer-events: auto;
    }
}

.button {
    flex: 0 0 auto;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 0;
    font-weight: 500;
    color: $link-color;
    cursor: pointer;
    user-select: none;
}

.legacy {
    background-color: $legacy-background-light;
    padding: 0 16px;
    border-radius: 4px;
    height: 44px;

    .tag {
        padding: 14px 0;

        &::after {
            border-radius: 4px;
            transform: scale(0.9);
        }

        &.copied::after {
            transform: scale(1);
        }
    }

    .button {
        padding: 11px 16px;
    }
}
