@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: flex;
    flex-direction: column;
    color: #333;
    align-items: center;
}

.avatarPreview {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #ccc;
    flex-shrink: 0;

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 93px;
    height: 32px;
    padding: 0 15px;
    color: #333;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    line-height: 32px;
    position: relative;
    flex-shrink: 0;
    border-radius: 100px;
    border: 1px solid #333;
    margin: 16px 0 0 0;
    @include crop();
}

.fileInput {
    opacity: 0;
    display: block;
    width: 375px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    cursor: pointer;

    * {
        cursor: pointer;
    }
}

.errorsContainer {
    text-align: center;
}
