@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex: 1;

    > * {
        transition: flex 0.3s, flex-basis 0.3s, opacity 0.3s, transform 0.3s;
    }
}

.focused {
    // TODO: add focus state
}

.input {
    display: block;
    width: 100%;
    word-break: break-word;
    font-size: 14px;
    line-height: 24px;
    color: #333;
    max-height: 40vh;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: transparent;
    appearance: none;
    border: none;
    resize: none;
    outline: none;
    padding: 0;

    &:focus {
        outline: 0;
    }

    @media all and (max-width: $mobile-breakpoint) {
        font-size: 16px;
    }

    &Container {
        padding: 6px 15px;
        display: flex;
        border-radius: 18px;
        background-color: #F2F2F2;
        position: relative;
        flex: 1;
        align-items: center;
    }
}

.btn {
    height: 36px;
    width: 36px;
    color: #fff;
    background-color: #000;
    transition: transform 0.3s;
    margin-left: 15px;

    &Wrap {
        overflow: hidden;
        flex-grow: 0;
        flex-basis: 0;
        opacity: 0;
        transform: scale(0.6);

        .filled & {
            flex-basis: 55px;
            transform: scale(1);
            opacity: 1;
        }
    }

    &:hover, &:focus {
        transform: rotate(90deg);
    }
}

.children {
    margin-left: 15px;
    flex: 0 0;
}
