@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: flex;
    flex-direction: column;
    color: #333;
}

.coverPreview {
    width: 100%;
    height: 200px;
    overflow: hidden;
    background-color: #eee;
    flex-shrink: 0;
    border-radius: 10px;
    position: relative;

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.buttons {
    display: flex;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 16px;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 93px;
    height: 32px;
    padding: 0 15px;
    color: #333;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    line-height: 32px;
    position: relative;
    flex-shrink: 0;
    border-radius: 8px;
    border: 1px solid #fff;
    margin: 16px 0 0 0;
    cursor: pointer;
    background-color: rgba(255,255,255,1);
    @include crop();
}

.fileInput {
    opacity: 0;
    display: block;
    width: 375px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    cursor: pointer;

    * {
        cursor: pointer;
    }
}

.emptyMessage {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 16px;
    color: #ccc;
    pointer-events: none;
}
