@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    margin: 16px 0 0 0;
    padding: 0 16px 16px 16px;
    transition-delay: 0.4s !important;

    &.slideDown {
        transform: translateY(100px);
        opacity: 0;
    }

    &.animated {
        opacity: 1;
        transform: translateY(0);
        transition: transform 1s ease, opacity 1s ease;
    }

    &.hasCollections {
        margin: 10px 0 0 0;
    }

    .moreBtnContainer {
        display: flex;
        justify-content: center;

        // @media all and (max-width: 767px) {
        //     display: none;
        // }

        .moreBtn {
            display: block;
            width: 100%;
            height: auto;
            padding: 11px 20px;
            border-radius: 10px;
            font-weight: 500;
            font-size: 14px;
            line-height: 18px;
            background-color: #fff;
            color: #000;
            margin: 0;
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
            transition: transform 0.3s ease, box-shadow 0.3s ease;

            @media all and (min-width: $tablet-landscape-breakpoint) {
                &:hover {
                    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
                    transform: translateY(-2px);
                }
            }
        }
    }
}

.heading {
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #333;
    margin: 0 0 15px 0;
    display: flex;
    width: 100%;

    > div {
        @include crop();
    }

    > span {
        display: block;
        font-weight: normal;
        color: #8e8e8e;
        padding: 0 0 0 4px;
        position: relative;
        top: 1px;
        flex-shrink: 0;
    }
}

.productsContainer {
    transform: translateY(50px);
    opacity: 0;
    animation: reveal .8s ease forwards;

    &:first-child {
        margin: 6px 0 0 0;
    }
}

@keyframes reveal {
    0% {
        transform: translateY(50px);
        opacity: 0;
    }
    100% {
        transform: none;
        opacity: 1;
    }
}
