@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-wrap: wrap;
    height: auto;
}

.colorsList {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin: 0;

    > label {
        position: relative;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        margin: 0 10px 0 0;
        cursor: pointer;
        overflow: hidden;

        @media all and (max-width: $tablet-portrait-breakpoint) {
            margin: 0 10px 10px 0;
        }

        @media all and (max-width: 340px) {
            margin: 0 6px 0 0;
        }

        > input {
            display: none;
        }

        > svg {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .input {
        display: none;
    }
}

.customColorBtn {
    position: relative;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin: 0 10px 0 0;
    cursor: pointer;

    > img {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }

    > svg {
        pointer-events: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.hexInputContainer {
    width: 100%;
    max-width: 100%;
    margin: 10px 0 0 0;
}

.check {
    color: #fff;
}

.disabled {
    pointer-events: none;
    opacity: 0.6;
}
