@import '@mssgme/scss-basics/vars';

.root {
    display: none;
    width: 20px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    cursor: pointer;
    z-index: 30;
    justify-content: center;
    align-items: center;
    outline: none;

    > svg {
        color: #fff;
    }

    @media all and (max-width: $mobile-breakpoint) {
        display: flex;
    }
}
