.root {
    position: relative;
    padding: 0 16px;
    border-bottom: 1px solid #f2f2f2;
    height: var(--modal-header-height);
    display: flex;
    align-items: center;
    width: 100%;
    padding-top: var(--modal-header-padding-top);

    &.light {
        background-color: #fff;
    }

    &.noBorder {
        border-bottom: 0;
    }
}

.hasHeader {
    padding-top: var(--safe-area-inset-top);
}

.title {
    display: block;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.6;
    white-space: nowrap;
    text-overflow: ellipsis;
    position: absolute;
    top: calc(50% + var(--modal-header-padding-top) / 2);
    left: 0;
    transform: translateY(-50%);
    padding: 0 106px;
    width: 100%;
    overflow: hidden;
    pointer-events: none;
    text-align: center;
}

.rightButton {
    position: absolute;
    bottom: 0;
    right: 0;
    height: var(--modal-header-inner-height);
    display: flex;
    flex-direction: row;
}

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50px;
    height: var(--modal-header-inner-height);
    cursor: pointer;
}

.bottomContent {
    padding: 0 16px 16px 16px;
    background-color: #fff;
}
