@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    color: #000;
    position: relative;
    background-color: #fff;
    padding: 8px 0 8px 32px;

    &::before {
        content: '';
        display: block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #3fb990;
        position: absolute;
        left: 16px;
        top: calc(50% - 4px);
        pointer-events: none;
    }

    &.disabled {
        &::before {
            background-color: #ccc;
        }
        opacity: 0.4;
        filter: grayscale(1);
    }
}

.productRoot {
    width: calc(100% - 40px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &.fullWidth {
        width: 100%;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            .title {
                color: $link-color;
            }
        }
    }
}

.thumbnail {
    background-color: #efefef;
    overflow: hidden;
    position: relative;
    width: 50px;
    height: 50px;
    flex-basis: 50px;
    min-width: 50px;
    padding-top: 0;
    box-shadow: none;
    border-radius: 8px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.emptyPlaceholder {
    width: 100%;
    height: 100%;
    background-color: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
}

.meta {
    flex: 1;
    max-width: calc(100% - 50px);
    padding: 0 10px;
}

.title {
    display: block;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: #000;
    margin: 0;
    width: 100%;
    transition: color 0.3s ease;
    @include crop();
}

.price {
    @include crop();
    display: block;
    font-weight: normal;
    font-size: 12px;
    line-height: 12px;
    color: #999;
    margin: 8px 0 0 0;
}

.discountPrice {
    font-weight: bold;
    color: #000;
    margin: 0 8px 0 0;
}

.lineThrough {
    text-decoration: line-through;
}

.dragHandle {
    flex-basis: 40px;
    min-width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: grab;

    svg {
        pointer-events: none;
        transition: color 0.3s ease;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            svg {
                color: $link-color;
            }
        }
    }
}
