@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    padding: 20px;
    -webkit-overflow-scrolling: touch;

    @media all and (max-width: $mobile-breakpoint) {
        display: block;
        padding: 0;
    }
}

.content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
