@import '@mssgme/scss-basics/vars';

.root {
    width: calc(100% + 32px);
    overflow: hidden;
    margin: 0 0 0 -16px;
    position: relative;
}

.list {
    width: 100%;
    display: flex;
    overflow: auto;
    list-style: none;
    margin: 0;
    padding: 6px 0;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;

    &::before,
    &::after {
        content: '';
        display: block;
        padding: 4px 8px;
    }
}

.list::-webkit-scrollbar {
    width: 0;
    height: 0;
}
.list::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}
.list::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0);
}

.item {
    text-align: center;
    display: block;
    flex-grow: 1;
    margin: 0 6px 10px 0;
    user-select: none;

    &:last-of-type {
        margin: 0 0 10px 0;
    }

    a {
        overflow: hidden;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        cursor: pointer;
        font-weight: normal;
        display: block;
        padding: 6px 16px;
        position: relative;
        color: #000;
        white-space: nowrap;
        transition: box-shadow .3s ease, transform .3s ease;

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover {
                transform: translateY(-2px);
                box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
            }
        }

        &:active {
            transform: none;
            box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
        }
    }
}

.controlContainerLeft {
    height: 100%;
    width: 44px;
    background-image: linear-gradient(90deg, #fafafa 0%, #fafafa 60%, rgba(250,250,250,0) 100%);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    cursor: pointer;

    .control {
        right: 10px;

        &:hover {
            transform: translateX(-3px);
        }
    }
}

.controlContainerRight {
    height: 100%;
    width: 44px;
    background-image: linear-gradient(-90deg, #fafafa 0%, #fafafa 60%, rgba(250,250,250,0) 100%);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    cursor: pointer;

    .control {
        left: 10px;

        &:hover {
            transform: translateX(3px);
        }
    }
}

.control {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 6px;
    transition: transform .3s ease;
}
