@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 50px;

    @media all and (max-width: $tablet-landscape-breakpoint) {
        padding: 20px;
    }

    @media all and (max-width: $mobile-breakpoint) {
        padding: 16px;
    }
}

.content {
    margin: auto;

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
    }
}

.description {
    font-size: 1.143em;
    text-align: center;
    margin-bottom: 30px;
}

.heading {
    text-align: center;
    margin-top: 20px;
    position: relative;
}

.footer {
    margin-top: 30px;
    text-align: center;

    @media all and (max-width: $mobile-breakpoint) {
        margin-top: 14px;
    }
}

.badge {
    position: relative;
    top: -20px;
}
