.root {
    display: flex;
    justify-content: center;
    margin: 12px;
    font-size: 12px;
    color: #555;
}

.button {
    display: block;
    background-color: rgba(142, 142, 147, 0.1);
    padding: 8px 20px;
    text-align: center;
    transition: opacity .3s ease;
    border-radius: 8px;
    cursor: pointer;
}

.disabled {
    opacity: .5;
    pointer-events: none;
}
