@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';
@import '@mssgme/scss-basics/mixins/clearfix';

.root {
    flex: 0 0 auto;
    position: relative;
    display: block;
    padding: 12px 0;
    cursor: pointer;
    background-color: #fff;
    text-decoration: none;
    border-radius: 10px;
    transition: padding 0.3s ease, background-color 0.3s ease;
    @include clearfix;

    &.active {
        padding: 12px 16px;
        background-color: rgba(142, 142, 147, 0.1);
    }
}

.body {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.bodyRight {
    width: calc(100% - 52px);
}

.head,
.content {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.content {
    margin: 3px 0 0 0;
    position: relative;
}

.avatar {
    width: 40px;
    height: 40px;
    margin-right: 12px;
}

.username {
    position: relative;
    color: #333;
    font-size: 16px;
    font-weight: normal;
    line-height: 19px;
    flex: 1;
    @include crop();
}

.time {
    font-weight: normal;
    color: #8e8e8e;
    font-size: 12px;
    line-height: 14px;
}

.message {
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #8e8e8e;
    margin: 0;
    @include crop();
}

.blockedClient {
    color: rgba(255, 150, 150, 1);
    font-style: italic;
}

.status {
    position: absolute;
    display: block;
    left: 0;
    top: 50%;
    width: 14px;
    transform: translateY(-50%);

    ~ span {
        margin-left: 20px;
    }
}

.done {
    color: #3eb991;
}

.snooze {
    color: #ffc82c;
}

.newDot {
    display: block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #007aff;
    flex: 0 0 auto;
}

.platformIcon {
    display: block;
    flex: 0 0;
    opacity: 0.5;
    user-select: none;
}
