.root {
    display: flex;
    width: 100%;
    height: 70px;
    align-items: center;
    padding: 0 28px;
}

.logo {
    display: block;
    width: auto;
    height: 30px;
    margin-right: auto;

    > svg {
        display: block;
        height: 100%;
        width: auto;
    }
}

.link {
    display: block;
    font-weight: 600;
    font-size: 15px;
    color: #2774F2;
}
