@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    position: relative;
    height: 68px;
    // margin: 0 25px;
    padding: 0 25px;
    border-bottom: 1px solid #f2f2f2;

    @media all and (max-width: $mobile-breakpoint) {
        padding: 0 10px;
        margin: 0;
    }
}

.content {
    height: 100%;
    flex: 1;
}

.buttons {
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    transition: 0.5s transform;
    overflow: hidden;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;

    > * {
        margin-left: 15px;
        &:first-child {
            margin-left: 0;
        }
    }
}

.backBtn {
    margin-right: 10px;
}
