.root {
    padding: 24px 16px 16px 16px;
    text-align: center;

    img {
        display: block;
        height: 40px;
        width: auto;
        margin: 0 0 10px 10px;
    }
}

.title {
    font-weight: bold;
    font-size: 20px;
    line-height: 26px;
    margin: 0 0 6px 0;
}

.text {
    font-size: 16px;
    line-height: 22px;
    color: #8e8e93;
    margin: 0 0 16px 0;
}

.center {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.spaced {
    margin-bottom: 8px;
}
