@import '@mssgme/scss-basics/vars';

.root {
    width: 100%;
    height: 100%;
}

.wrapper {
    width: 375px;
    max-width: 100%;
    margin: 0 auto;
    height: 100%;

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
    }
}

.groupButtonsContainer {
    display: flex;
    justify-content: center;
    margin: 0 0 30px 0;

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
        margin: 15px 0;
    }
}

.widgetButtonContainer {
    position: relative;
    display: flex;
    margin: 10px 0 0 0;

    @media all and (max-width: $mobile-breakpoint) {
        margin: 10px 15px 0 15px;
    }

    &.right {
        justify-content: flex-end;
    }

    &.left {
        justify-content: flex-start;
    }

    > .widgetButton {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-color: #ccc;
        cursor: pointer;

        &:hover {
            .hand {
                animation: 1s hand_wave ease;
            }
        }
    }
}

.icon {
    color: #fff;
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    opacity: 0;
    pointer-events: none;
    transition: transform 0.5s ease, opacity 0.5s ease;

    &.close {
        top: calc(50% - 8px);
        left: calc(50% - 8px);
        transform-origin: 50% 50%;
        transform: rotate(120deg) scale(1);
    }

    &.hand {
        width: 30px;
        height: 30px;
        top: calc(50% - 15px);
        left: calc(50% - 13px);
        transform-origin: 50% 75%;
        transform: scale(0.5) rotate(-120deg);
    }

    &.visible {
        opacity: 1;
        pointer-events: auto;
        transform: none;
        transform: scale(0.99) rotate(0);
    }
}

.content {
    display: flex;
    flex-direction: column;
    max-height: 100%;
    position: relative;

    @media all and (max-width: $mobile-breakpoint) {
        width: calc(100% - 30px);
        margin: 0 auto;
        min-height: auto;
        height: calc(100vh - 364px);
    }

    .cardContainer {
        opacity: 0;
        pointer-events: none;
        transform: translateY(15px);
        transition: transform 0.4s ease, opacity 0.4s ease;
        transition-delay: 0s;

        &.cardVisible {
            opacity: 1;
            pointer-events: auto;
            transform: translateY(0);
            transition-delay: 0.4s;
        }

        @media all and (max-width: $mobile-breakpoint) {
            width: 100%;
            height: 100%;
            border-radius: 10px;
            box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.08);
            overflow: hidden;
        }
    }

    > div {
        flex-grow: 1;
    }
}

.welcome {
    position: absolute;
    transform: translateY(calc(-100% + 5px));
    transition: transform 0.4s ease, opacity 0.4s ease;
    top: 0;
    right: 0;
    opacity: 0;
    pointer-events: none;
    max-width: 290px;
    min-width: 100px;
    align-self: flex-end;
    flex-grow: 0;
    margin-top: auto;
    padding: 12px 20px;
    background-color: #fff;
    border-radius: 6px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.06);
    transition-delay: 0s;

    &.visibleWelcome {
        transform: translateY(calc(-100% - 10px));
        opacity: 1;
        pointer-events: auto;
        transition-delay: 0.4s;
    }

    &.left {
        left: 0;
        right: auto;
    }

    &.right {
        right: 0;
        left: auto;
    }

    > h6 {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        margin: 0;
    }

    > p {
        color: #888;
        font-size: 13px;
        line-height: 18px;
        margin: 0;
        white-space: pre-wrap;
    }
}

@keyframes :global(hand_wave) {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(12deg);
        transform: rotate(12deg);
    }
    75% {
        -webkit-transform: rotate(-17deg);
        transform: rotate(-17deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
}
