.searchHeader {
    display: block;
    padding: 6px 16px 16px 16px;
}

.searchInputContainer {
    position: relative;
}

.searchInput {
    display: block;
    width: 100%;
    height: 36px;
    appearance: none;
    background-color: rgba(142,142,147,0.1);
    border-radius: 10px;
    padding: 0 16px 0 36px;
    box-shadow: none;
    margin: 0;
    outline: none;
    border: none;
    font-size: 16px;
}

.searchIcon {
    width: 36px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
}
