@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    overflow: hidden;
    animation: opacity 1s ease forwards;
}

@keyframes opacity {
    0% {
        background-color: rgba(0, 0, 0, 0);
    }
    100% {
        background-color: rgba(0, 0, 0, 0.4);
    }
}

@keyframes reveal {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0);
    }
}

.modalPane {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    height: 470px;
    max-height: calc(100% - 44px);
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #fff;
    border-radius: 8px 8px 0 0;
    overflow: hidden;
    animation: reveal 0.6s ease forwards;
}

.header {
    width: 100%;
    height: 44px;
    background-color: #ccc;
    position: relative;
    padding: 0 50px;

    &.green {
        background-color: #3eb991;
    }

    &.red {
        background-color: #ec565d;
    }

    > h1 {
        width: 100%;
        font-weight: 500;
        font-size: 14px;
        line-height: 44px;
        color: #fff;
        text-align: center;
        margin: 0;
        @include crop();
    }
}

.commentArea {
    display: flex;
    flex: 1;
    width: 100%;
    border: none;
    outline: none;
    resize: none;
    padding: 26px;
    font-size: 16px;
    line-height: 26px;
}

.commentArea::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #8e8e93;
}
.commentArea::-moz-placeholder {
    /* Firefox 19+ */
    color: #8e8e93;
}
.commentArea:-ms-input-placeholder {
    /* IE 10+ */
    color: #8e8e93;
}
.commentArea:-moz-placeholder {
    /* Firefox 18- */
    color: #8e8e93;
}

.footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 64px;
    padding: 16px 16px 0 16px;
}

.footerMeta {
    display: block;
    font-size: 12px;
    line-height: 18px;
    color: #8e8e93;
}

.closeButton {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 100%;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    transition: transform 0.6s ease;

    &:hover {
        transform: rotate(-90deg);
    }
}
