@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    width: 100%;
    margin-bottom: 16px;
    flex: 0 0 auto;

    &.reverse {
        flex-direction: row-reverse;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.avatar {
    margin-right: 10px;

    .reverse & {
        margin-right: 0;
        margin-left: 10px;
    }
}

.messages {
    flex: 1;
}
