@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    background-color: #fafafa;
}

.headerBtns {
    display: flex;
    align-items: center;
}

.headerLink {
    display: flex;
    align-items: center;
    height: 34px;
    margin: 0 0 0 10px;
}

.navItem {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    height: 51px;
    background-color: #fff;
    padding: 0 10px 0 16px;
    font-weight: normal;
    font-size: 16px;
    color: #000;
}

.navItemLabel {
    display: block;
    flex: 1;
    padding: 0 10px 0 0;
    @include crop();
}


.navItemMeta {
    display: flex;
}

.navItemMetaText {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #8E8E8E;
}

.navItemIcon {
    margin: 0 0 0 6px;
    color: #8E8E8E;
}

.storeFrontHeader {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
    justify-content: space-between;
    align-items: center;
    padding: 8px 16px;
    margin: 8px 0 0 0;
    color: rgba(0,0,0,.5);
}

.storeFrontHeaderHeading {
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
}

.storeFrontHeaderCounter {
    display: block;
    font-size: 14px;
    line-height: 16px;
}

