@import '@mssgme/scss-basics/vars';

.root {
    flex: 0 0 auto;
    text-align: center;
    position: sticky;
    top: -30px;
    z-index: 1;
    display: flex;
    justify-content: center;
    margin: 0 0 16px 0;

    .content {
        min-width: 250px;
        background: #fafafa;
        padding: 8px;
        color: #8E8E8E;
        font-weight: 500;
        font-size: 13px;
        border-radius: 10px;
    }
}
