@import '@mssgme/scss-basics/mixins/crop';

.backdrop {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000, $alpha: 0.5);
    z-index: 1;
}

.root {
    display: flex;
    position: absolute;
    flex-direction: column;
    width: 100%;
    max-height: 90%;
    bottom: 56px;
    left: 0;
    background-color: #fff;
    z-index: 2;
}

.header {
    padding: 24px 24px 12px;
}

.content {
    padding: 12px 24px 24px;
}

.title {
    display: block;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    color: #333;
    margin: 0;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
}
