.root {
}

.strengthMeter {
    display: block;
    width: 100%;
    height: 3px;
    margin-top: 4px;
    border: none;
    background-color: red;
    text-align: center;

    &.withLabel {
        font-size: 0.7em;
        padding: 4px;
        height: unset;
    }

    &.strength-0 {
        // weak
        background-color: red;
        color: white;
    }

    &.strength-1 {
        // moderate
        background-color: blue;
        color: white;
    }

    &.strength-2 {
        // arguably strong
        background-color: green;
        color: white;
    }
}

.strength-0 {
    // weak
}

.strength-1 {
    // moderate
}

.strength-2 {
    // arguably strong
}
