@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    width: 100%;
    color: #000;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 6px;
    align-items: baseline;
}

.disabled {
    pointer-events: none;
    opacity: 0.6;
}

.requiredMark {
    margin-left: 4px;
}

.optionalMark {
    margin-left: 6px;
    color: $placeholder-color;
    font-weight: lighter;
    font-size: 12px;
}

.builderStyle {
    padding-left: 10px;
    color: $font-default-color;
    margin-bottom: 4px;
    position: relative;
}

.badge {
    position: absolute;
    justify-self: end;
    right: 8px;
    bottom: -1px;
    z-index: 1;
}
