@import '@mssgme/scss-basics/mixins/crop';

.copy {
    background-color: #f5f5f5;
    padding: 10px 10px 24px 10px;
    margin: 15px 0;
    border-radius: 6px;
    position: relative;

    span {
        font-weight: 500;
        color: #f5a623;
    }

    i {
        position: absolute;
        top: 0;
        left: 0;
        pointer-events: none;
        opacity: 0;
    }
}

.button {
    display: block;
    border-radius: 6px;
    width: 100%;
    font-size: 14px;
    line-height: 24px;
    padding: 10px;
    text-align: center;
    color: #fff;
    background-color: #0284ff;
    @include crop();

    span {
        text-transform: capitalize;
    }
}
