@import '@mssgme/scss-basics/vars';

.root {
    position: relative;
    margin: 16px;
}

.input {
    display: block;
    width: 100%;
    box-shadow: none;
    border: none;
    outline: none;
    background-color: #f2f2f2;
    appearance: none;
    font-size: 17px;
    line-height: 22px;
    padding: 7px 16px 7px 40px;
    border-radius: 10px;
}

.icon {
    display: block;
    position: absolute;
    left: 10px;
    top: calc(50% - 10px);
    pointer-events: none;
}
