.root {
    display: block;
    margin: 12px 0 0 0;
    height: 32px;
    font-weight: 500;
    font-size: 14px;
    line-height: 32px;
    color: #007aff;
    text-align: center;
    cursor: pointer;
    transition: color 0.3s ease;

    &:hover {
        color: darken(#007aff, 20%);
    }
}

.disabled {
    color: #888;
    pointer-events: none;
}
