@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

@keyframes revealOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes revealTransform {
    0% {
        transform: translateY(200px);
        opacity: 0;
    }
    100% {
        transform: none;
        opacity: 1;
    }
}

.bg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
    animation: revealOpacity 0.5s ease forwards;
}

.content {
    width: calc(100% - 20px);
    height: calc(100% - 64px);
    position: absolute;
    bottom: 32px;
    left: 10px;
    pointer-events: none;
    animation: revealTransform 0.5s ease forwards;
}

.cancel {
    display: block;
    padding: 16px;
    background-color: #fff;
    border-radius: 13px;
    margin: 8px 0 0 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.4px;
    text-align: center;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    pointer-events: auto;
    @include crop();
}

.listContainer {
    max-height: calc(100% - 65px);
    background-color: #fff;
    border-radius: 13px;
    text-align: center;
    position: absolute;
    bottom: 65px;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    pointer-events: auto;
}

.heading {
    display: block;
    width: 100%;
    min-height: 58px;
    padding: 18px 16px 14px 16px;
    font-size: 16px;
    line-height: 25px;
    letter-spacing: 0.3px;
    color: #8e8e8e;
    border-bottom: 1px solid #cbcbcb;
    @include crop();
}

.linksList {
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 25px;
    overflow: auto;

    > li {
        border-top: 1px solid #cbcbcb;

        &:first-of-type {
            border: none;
        }

        > a {
            display: block;
            padding: 16px;
            font-weight: normal;
            color: #000;
            @include crop();
        }
    }
}

.linksList::-webkit-scrollbar {
    width: 0;
    background-color: rgba(255, 255, 255, 0);
}

.linksList::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background-color: rgba(255, 255, 255, 0);
    -webkit-transition: background-color 0.6s ease;
    transition: background-color 0.6s ease;
}
