@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: flex;
    flex-direction: column;
    width: 375px;
    max-width: 100%;
    flex: 0 0 auto;
    position: relative;
    background-color: #fff;
    border-right: 1px solid #f2f2f2;

    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.6s ease;
    }

    &.faded::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 1;
        pointer-events: auto;
    }

    @media all and (max-width: $tablet-portrait-breakpoint) {
        width: 300px;
    }

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
        height: calc(100% - 44px);
    }
}

