@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.label {
    display: block;
    width: 80px;
    max-width: 100%;
    font-size: 16px;
    line-height: 19px;
    color: $placeholder-color;
}

.inputContainer {
    position: relative;
    width: calc(100% - 80px);
    padding: 0 0 0 25px;

    &:only-child {
        width: 100%;
        padding: 0;
    }
}

.input {
    font-size: 16px;
    line-height: 19px;
    color: $font-default-color;

    &::placeholder {
        color: $primary;
    }

    &:not(:focus) {
        cursor: pointer;
    }

    &:focus {
        &::placeholder {
            color: transparent;
        }
    }
}

input.input,
select.input {
    font-size: 16px !important;
    line-height: 19px !important;
    color: $font-default-color !important;
    border-color: transparent !important;

    &[dataValue=''] {
        color: $primary;
        cursor: pointer;
    }

    &:focus {
        border-color: transparent;
    }

    &:hover {
        border-color: transparent;
    }
}

textarea.input {
    display: block;
    width: 100%;
    padding: 7px 0;
    appearance: none;
    outline: none;
    border: none;
    border-radius: 0;
    resize: none;

    &:not([data-value='']) {
        padding: 0 0 0 11px;
        border-left: 2px solid $warning;
    }
}

.icon {
    display: block;
    position: absolute;

    &.start {
        top: 5px;
        left: 0;
    }

    &.end {
        bottom: 6px;
        right: 2px;
        top: inherit;
        left: inherit;
    }
}

.danger {
    font-size: 16px;
    color: $danger;
    margin: 3px 0 0 4px;
}

.success {
    font-size: 24px;
    color: $success;
}

.readOnly .input {
    cursor: text;
}
