@import '@mssgme/scss-basics/mixins/clearfix';
@import '@mssgme/scss-basics/mixins/crop';
@import '@mssgme/scss-basics/vars';

.root {
    width: 100%;
    height: 100%;
    background-color: #fafafa;

    .inner {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow: auto;
        -webkit-overflow-scrolling: touch;

        @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
            transform: translate3d(0px, 0px, 0px);
        }
    }

    .inner::-webkit-scrollbar {
        width: 0;
    }

    .inner::-webkit-scrollbar-track {
        background: rgba(255, 255, 255, 0);
    }

    .inner::-webkit-scrollbar-thumb {
        background: rgba(255, 255, 255, 0);
    }
}

.productInfo {
    position: relative;

    .content {
        padding: 0 16px;
        background-color: #fff;
    }

    > .content {
        padding: 0 16px 16px 16px;
    }
}

.productInfoHeaderPane {
    width: 100%;
    padding-top: 16px;
    background-color: #fff;
    position: relative;
    z-index: 2;

    .productInfoHeaderPaneMeta {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        .productInfoHeaderPaneMeta {
            //width: calc(100% - 36px);
            width: 100%;
            //padding: 0 20px 0 2px;
            padding: 0 0 0 2px;

            h1 {
                width: 100%;
                font-size: 16px;
                line-height: 1.5;
                font-weight: normal;
                color: #333;
                margin: 0;
            }

            .priceContainer {
                margin: 3px 0 0 0;
                width: 100%;
                font-weight: bold;
                font-size: 16px;
                line-height: 19px;
                color: #000;
                @include crop();

                .oldPrice {
                    font-weight: 500;
                    text-decoration: line-through;
                    margin: 0 0 0 10px;
                    color: rgba(0, 0, 0, 0.3);
                }
            }
        }

        .productInfoHeaderPaneShare {
            width: 36px;
            height: 36px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
        }
    }
}

.productInfoDescription {
    margin: 16px 0 0 0;

    h2 {
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
        margin: 0;
    }

    p {
        font-size: 14px;
        line-height: 21px;
        color: #333333;
        margin: 8px 0 0 0;
        word-break: break-word;
        white-space: pre-wrap;
    }
}

.storePane {
    display: block;
    margin: 8px 0 0 0;
    padding: 16px;
    background-color: #fff;

    .storePaneContent {
        width: 100%;
        display: flex;
        align-items: center;

        .storePaneMeta {
            display: flex;
            width: calc(100% - 20px);
            align-items: center;

            .storePaneAvatar {
                width: 60px;
                height: 60px;
                border-radius: 50%;
                overflow: hidden;

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .storePaneInfo {
                width: calc(100% - 60px);
                padding: 0 10px 0 16px;
                white-space: nowrap;

                h3 {
                    width: 100%;
                    font-weight: bold;
                    font-size: 24px;
                    line-height: 28px;
                    color: #333;
                    margin: 0;
                    @include crop();
                }

                span {
                    display: block;
                    width: 100%;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 14px;
                    color: #8e8e8e;
                    margin: 2px 0 0 0;
                    @include crop();
                }
            }
        }

        .storePaneIcon {
            width: 20px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #8e8e8e;
        }
    }
}

.sharingPane {
    width: 100%;
    padding: 16px;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 11;
    background-color: #fff;
    transform: translateY(150%);
    transition: transform 0.3s ease;

    &.visible {
        transform: none;
    }

    .shareList {
        list-style: none;
        margin: 0;
        padding: 0;

        > li {
            max-width: 100%;
            margin: 0 0 7px 0;

            &:last-of-type {
                margin: 0;
            }

            > a {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 38px;
                border-radius: 4px;
                font-weight: normal;
                font-size: 15px;
                color: #fff;
                background-color: #000;

                > svg {
                    margin: -1px 6px 0 0;
                }

                &.twitter {
                    background-color: #1da1f2;
                }

                &.facebook {
                    background-color: #0284ff;
                }

                &.vkontakte {
                    background-color: #4f78a3;
                }
            }
        }
    }
}

.overlayBg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    z-index: 15;
    pointer-events: none;
    transition: opacity 0.3s ease;

    &.visible {
        opacity: 1;
        pointer-events: auto;
        z-index: 10;
    }
}

.modal {
    width: calc(100% - 32px);
    border-radius: 10px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 16px;
    z-index: 15;
    opacity: 0;
    pointer-events: none;
    transition: transform 0.3s ease, opacity 0.3s ease;
    padding: 16px;

    &.visible {
        opacity: 1;
        pointer-events: auto;
        transform: translateY(-50%);
        transition: transform 0.6s ease, opacity 0.6s ease;
    }

    .modalTitle {
        display: block;
        font-size: 15px;
        line-height: 25px;
        font-weight: 500;
        text-align: center;
    }

    .copyText {
        background-color: #f5f5f5;
        padding: 10px 10px 24px 10px;
        margin: 15px 0;
        border-radius: 6px;
        position: relative;

        span {
            font-weight: 500;
            color: #f5a623;
        }

        i {
            position: absolute;
            top: 0;
            left: 0;
            pointer-events: none;
            opacity: 0;
        }
    }

    .messengerBtn {
        display: block;
        border-radius: 6px;
        width: 100%;
        font-size: 14px;
        line-height: 24px;
        padding: 10px;
        text-align: center;
        color: #fff;
        background-color: #0284ff;
        @include crop();

        span {
            text-transform: capitalize;
        }
    }
}

.websiteLinkContainer {
    display: block;
    margin: 16px 0 0 0;
    cursor: pointer;
    background-color: #fff;
    border-radius: 10px;
    padding: 8px 20px;
    font-weight: 500;
    color: #000;
    text-align: center;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    min-height: 46px;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    user-select: none;

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
            transform: translateY(-2px);
        }
    }
}

.linkName {
    display: block;
    width: 100%;
    @include crop();
}

.linkDomain {
    display: block;
    width: 100%;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: #8e8e8e;
    @include crop();
}
