@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex: 1;
    width: 100%;
    overflow: hidden;
    background: #fafafa;
}

.container {
    display: flex;
    height: 100%;
    max-width: 100%;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
    overflow: auto;
    // -webkit-overflow-scrolling: touch;

    @media all and (max-width: $mobile-breakpoint) {
        padding: 0;
    }
}

.mobileBackButton {
    display: none;
    width: 100%;
    height: 50px;
    background-color: #fff;
    font-weight: normal;
    font-size: 16px;
    line-height: 50px;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    box-shadow: 0 -1px 0 0 rgba(0, 0, 0, 0.1);
    color: #007aff;

    @media all and (max-width: $mobile-breakpoint) {
        display: block;
    }
}

.sidebar {
    width: 375px;
    max-width: 100%;
    background: #fff;
    border-right: 1px solid #F2F2F2;
    height: 100%;
    position: relative;

    @media all and (max-width: $tablet-portrait-breakpoint) {
        width: 300px;
    }

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
    }
}
