@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media all and (max-width: $mobile-breakpoint) {
        position: absolute;
        width: 100%;
        height: calc(100% - 50px);
        top: 50px;
        left: 0;
        padding: 30px;
    }
}

.img {
    display: block;
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
}

.title {
    color: #1f2d3d;
    font-size: 24px;
    line-height: 34px;
    font-weight: normal;
    margin-top: 0;
    margin-bottom: 4px;

    @media all and (max-width: $mobile-breakpoint) {
        text-align: center;
    }
}

.description {
    color: #a9a9a9;
    font-size: 12px;
    line-height: 22px;
    margin: 0;

    @media all and (max-width: $mobile-breakpoint) {
        text-align: center;
    }
}
