@import '@mssgme/scss-basics/vars';

.root {
    position: relative;
    flex: 1 1 auto;
    z-index: 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.list {
    width: 100%;
    padding: 25px 25px 0;
    display: flex;
    flex-direction: column;

    @media all and (max-width: $mobile-breakpoint) {
        padding: 20px 10px;
    }
}

.downBtn {
    width: 30px;
    height: 30px;
    color: #fff;
    font-size: 40px;
    background-color: #000;
    box-shadow: 0 0 1px 1px #fff;

    &Wrap {
        position: absolute;
        display: inline-flex;
        right: 10px;
        bottom: 10px;
    }
}
