.help {
    margin: 10px 0 0 0;
    font-size: 13px;
    line-height: 18px;
    color: #8e8e93;
    white-space: pre-line;
}

.actions {
    text-align: right;
    margin: -7px 0 0 0;
}

.groupedElements {
    display: flex;
    justify-content: space-between;
}

.formNew > div {
    width: calc(100% - 48px);
    display: inline-block;
    margin-right: 16px;
}

.formNew button {
    top: -2px;
}

.formNew button svg {
    margin: 0px -8px 0px -8px;
}
