@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: #fafafa;
    transform: translateY(100%);
    transition: transform 0.6s ease;

    &.loaded {
        transform: none;
    }

    &.noAnimation {
        transition: none;
        transform: none;
    }
}

.header {
    width: 100%;
    height: 54px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    transition: box-shadow 0.3s ease;
    z-index: 1;

    &.shadow {
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    }
}

.backButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    height: 100%;
}

.heading {
    display: block;
    width: calc(100% - 108px);
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    position: absolute;
    top: calc(50% - 10px);
    left: 54px;
    text-align: center;
    margin: 0;
    @include crop();
}

.content {
    width: 100%;
    height: calc(100% - 54px);
    overflow: auto;
    position: absolute;
    top: 54px;
    left: 0;
    padding: 16px 15px;
    -webkit-overflow-scrolling: touch;
}

.content::-webkit-scrollbar {
    width: 0;
}

.content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

.content::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0);
}

.moreBtnContainer {
    display: flex;
    justify-content: center;

    .moreBtn {
        display: block;
        width: 100%;
        height: auto;
        padding: 11px 20px;
        border-radius: 10px;
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        background-color: #fff;
        color: #000;
        margin: 0;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        transition: transform 0.3s ease, box-shadow 0.3s ease;

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover {
                box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
                transform: translateY(-2px);
            }
        }
    }
}

.productItem {
    opacity: 0;
    transform: translateY(200px);
    animation: revealItem 1.2s ease forwards;
    animation-delay: 0.2s;

    &:nth-of-type(even) {
        animation-duration: 1.5s;
    }
}

@keyframes revealItem {
    0% {
        opacity: 0;
        transform: translateY(50px);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}
