@import '@mssgme/scss-basics/vars';

.root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 58px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
    padding: 0 16px;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0));
}

.logo {
    display: block;
    height: 30px;

    > img {
        display: block;
        height: 100%;
        width: auto;
    }
}

.button {
    display: flex;
    align-items: center;
    height: 36px;
    padding: 0 14px;
    border: 2px solid #ffffff;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    border-radius: 10px;
    transition: background-color 0.3s ease, color 0.3s ease;

    &:active {
        color: #fff;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            background-color: #fff;
            color: #000;
        }
    }
}
