.root {
    display: flex;
    align-items: center;
    width: 100%;
    height: 38px;
    border-radius: 10px;
    background-color: #f5f5f5;
    overflow: hidden;
    padding: 0 0 0 6px;
}

.actions {
    display: flex;
    height: 100%;
    margin-left: auto;
    padding: 6px;
    flex-shrink: 0;
}

.button {
    display: flex;
    height: 100%;
    min-width: 26px;
    padding: 6px 10px;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
    background-color: #007aff;
    border-radius: 6px;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #fff;
    cursor: pointer;

    &.remove {
        background-color: #dc5960;
        padding: 6px;
        margin-left: 6px;
    }
}

.fileInput {
    opacity: 0;
    display: block;
    width: 375px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    cursor: pointer;

    * {
        cursor: pointer;
    }
}

.spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.label {
    width: 100%;
    font-weight: 500;
    font-size: 14px;
    color: #333;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding: 0 8px;
}
