@import '@mssgme/scss-basics/vars';

.root {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;

    .content {
        flex-grow: 1;
        max-width: 100%;
        z-index: 10;
    }

    .overlay {
        position: absolute;
        z-index: 20;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.2);

        .prompt {
            margin: 20px;
            display: flex;
            justify-content: center;

            .box {
                border-radius: 10px;
            }

            .content {
                padding: 10px;

                text-align: center;
                line-height: 1.4;
            }

            .footer {
                display: flex;
                justify-content: center;
                padding: 0 5px;

                & > * {
                    margin: 5px;
                }
            }
        }
    }
}
