@import '@mssgme/scss-basics/vars';

.image {
    display: block;
    width: 250px;
    height: 250px;
    border-radius: 12px;
    object-fit: contain;

    @media all and (max-width: $mobile-breakpoint) {
        width: 180px;
        height: 180px;
    }
}

.link {
    display: block;
    text-decoration: none;
    margin: 0;
}

.message {
    padding: '5px 5px 6px 5px';
}

.timestamp {
    margin-right: 7;
    padding: '5px 0 0 0';
}

.text {
    margin-top: 6;
}
