@import '@mssgme/scss-basics/mixins/crop';

.fieldLabel {
    display: block;
    color: #000;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin: 0 0 6px 0;
}

.domainPreview {
    display: flex;
    width: 100%;
    height: 42px;
    align-items: center;
    padding: 0 16px;
    background-color: #f5f5f5;
    border-radius: 4px;
    border: 1px solid #c7c7cc
}

.domainValue {
    display: block;
    font-size: 16px;
    color: #8e8e93;
    flex: 1;
    padding: 0 10px 0 0;
    @include crop();
}

.domainButton {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    height: 100%;
}
