@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: #fff;
    box-shadow: 0 0 8px rgba(0,0,0,.1);
    border-radius: 8px;
    left: 25px;
    right: 25px;
    z-index: 100;
    bottom: 76px;
    min-height: 100px;
    max-height: 70vh;


    @media all and (max-width: $mobile-breakpoint) {
        left: 10px;
        right: 10px;
        max-height: calc(100vh - 100px);
    }
}

.header {
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    border-bottom: 1px solid #C7C7CC;
}

.title {
    display: block;
    flex: 1;
    width: 100%;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    margin: 0;
    @include crop();
}

.content {
    width: 100%;
    flex: 1;
    padding: 16px;
    overflow-y: auto;
    overflow-x: hidden;

    &:last-child {
        padding-bottom: 16px;
    }
}

.body {
    padding: 16px;
}

.footer {
    width: 100%;
    flex: 0 0 auto;
    font-size: 13px;
    line-height: 18px;
    color: #999;
    padding: 16px;
    border-top: 1px solid #C7C7CC;
}

.headerBefore, .headerAfter {
    display: flex;
    flex-basis: 60px;

    > * {
        margin-left: 10px;

        &:first-child {
            margin-left: 0;
        }
    }
}

.headerAfter {
    justify-content: flex-end;
}

.headerBefore {
    justify-content: flex-start;
}
