.rootContainer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.root {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(100%);
    transition: transform .6s ease;
    background-color: #fafafa;

    &.loaded {
        transform: none;
    }

    &.noAnimation {
        transform: none;
        transition:  none;
    }
}
