.root {
    min-width: 300px;
    flex-basis: 300px;
}

.messageWrap {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-bottom: 10px;
}

.avatar {
    height: 30px;
    flex-shrink: 0;
    margin-right: 14px;
}

.button {
    margin-left: 50px;
}

.message {
    font-size: 14px;
    line-height: 24px;
    border-radius: 20px 20px 20px 2px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    padding: 8px 20px;
    color: #29323c;
    background-color: #fff;
}
