.root {
    display: block;
    width: 100%;
}

.fetching {
    pointer-events: none;
}

.productsList {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
}

.listItem {
    margin: 0 0 8px 0;
}

.sortable {
    list-style: none;
    margin: 0;
    padding: 0;
    box-shadow: 0 0 20px rgba(0,0,0,.15);
    z-index: 1;
    pointer-events: auto !important;
    cursor: grabbing;

    [data-element="drag-handle"],
    a {
        cursor: grabbing;
    }
}
