@import '@mssgme/scss-basics/vars';

.photoContainer {
    position: relative;
    z-index: 1;
}

.photo {
    width: 100%;
    padding-top: 100%;
    position: relative;
    background-color: #f5f5f5;
    user-select: none;

    .photoContent {
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;

        .productImgContainer {
            width: 100%;
            height: 100%;

            > img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

    .photoPlaceholder {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #eee;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .controls {
        display: flex;
        flex-wrap: wrap;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        > a {
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 20%;
            height: 100%;
            padding: 16px;

            &.nextLink {
                left: 80%;
                justify-content: flex-end;
            }

            .paginationControl {
                width: 24px;
                height: 24px;
                border-radius: 50%;
                background-color: rgba(0, 0, 0, 0.3);
                display: flex;
                justify-content: center;
                align-items: center;
                opacity: 0;
                pointer-events: none;
                transform: scale(0.6);
                transition: transform 0.3s ease, opacity 0.3s ease;
                user-select: none;

                svg {
                    color: #fff;
                    transition: color 0.1s ease;
                }

                @media all and (max-width: $mobile-breakpoint) {
                    display: none;
                }
            }

            &:active {
                svg {
                    color: rgba(255, 255, 255, 0.5);
                }
            }

            &:hover {
                .paginationControl {
                    opacity: 1;
                    pointer-events: auto;
                    transform: scale(1);
                }
            }
        }
    }

    .closeBtn {
        position: absolute;
        top: 0;
        left: 0;
        width: 56px;
        height: 56px;
        z-index: 2;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;

        > div {
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.3);
            display: flex;
            justify-content: center;
            align-items: center;
            transition: transform 0.6s ease;

            > svg {
                color: #fff;
            }
        }

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover > div {
                transform: rotate(-90deg);
            }
        }
    }
}

.imagesList {
    margin: 0;
    padding: 0;
    list-style: none;
    height: 100%;
    width: 600%;
    display: flex;
    flex-wrap: wrap;
    transition: transform .6s ease;

    > li {
        width: 16.666666%;
        height: 100%;

        > img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.dotsContainer {
    position: absolute;
    bottom: 8px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-wrap: wrap;
}

.dot {
    width: 12px;
    height: 12px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
        content: '';
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #000;
        opacity: .3;
    }

    &.active::before {
        opacity: 1;
    }
}
