@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 46px;
    color: #000;
    border-radius: 4px;
    transition: color 0.3s ease, background-color .3s ease;
    margin: 0 0 8px 0;
    padding: 6px 0;
    position: relative;

    &.marked::after {
        content: '';
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #F14941;
        position: absolute;
        top: 4px;
        right: 9px;
        border: 2px solid #fafafa;
    }

    &.soon {
        color: #8E8E8E;
        pointer-events: none;
    }

    &.active {
        color: #fff;
        background-color: #000;
    }

    &:not(.active):hover {
        background-color: #eee;
    }

    &:not(.active):focus {
        color: inherit;
    }

    span {
        display: block;
        font-weight: bold;
        font-size: 9px;
        line-height: 9px;
        text-align: center;
        margin: 4px 0 0 0;

        i {
            font-weight: normal;
            font-style: normal;
        }
    }

    @media all and (max-width: $mobile-breakpoint) {
        flex-direction: row;
        justify-content: flex-start;
        padding: 8px;

        span {
            margin: 0 0 0 13px;
            font-size: 16px;
            font-weight: bold;
        }
    }
}

.iconContainer {
    svg {
        display: block;
        width: 20px;
        height: 20px;
        margin: 0 auto;
        color: currentColor;
    }

    @media all and (max-width: $mobile-breakpoint) {
        svg {
            width: 24px;
            height: 24px;
        }
    }
}
