.headingTitle {
    color: #243238;
    font-size: 18px;
    text-align: center;
}
.headingInstruction {
    display: block;
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.6);
    margin: 0 0 15px 0;
    text-align: center;
}
.sectionBlock {
    width: 100%;
    margin: 0 0 22px 0;
}
.copy {
    position: relative;
    box-sizing: border-box;
    padding: 16px;
    border-radius: 10px;
    background: #f5f5f5;
    cursor: pointer;
    &::after {
        content: attr(data-success);
        display: flex;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        position: absolute;
        top: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        background-color: #3eb991;
        font-weight: 500;
        color: #fff;
        opacity: 0;
        pointer-events: none;
        transform: scale(0.9);
        transition: transform 0.3s ease, opacity 0.3s ease;
    }
}
.copied::after {
    opacity: 1;
    transform: scale(1);
    pointer-events: auto;
}
