@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: relative;
    display: block;
    border-radius: 4px 0 0 4px;
    background-color: #f5f5f5;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #000;
    @include crop();

    padding: 11px 16px 11px 16px;

    &::after {
        content: '';
        display: block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #ccc;
        position: absolute;
        top: calc(50% - 4px);
    }

    &.enabled::after {
        background-color: #3eb991;
    }

    > span {
        color: #8e8e93;
    }

    &.right {
        padding-right: 30px;

        &::after {
            right: 15px;
        }
    }

    &.left {
        padding-left: 30px;

        &::after {
            left: 15px;
        }
    }
}
