.root {
    display: block;
    color: #333;
    font-size: 14px;
    line-height: 18px;
    margin: 16px 0 32px 0;
}

.body {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;

    &:before,
    &:after {
        content: '';
        display: block;
        height: 0;
        border-top: 1px solid #333;
        flex-grow: 1;
    }
}

.text {
    padding: 0 10px;
}
