@import '@mssgme/scss-basics/vars';

.root {
    flex: 1;
    // width: 100%;
    // height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    // @media all and (max-width: $mobile-breakpoint) {
    //     height: auto;
    // }
}

.content {
    width: 662px;
    max-width: 100%;
}

.heading {
    width: 100%;
    font-weight: bold;
    font-size: 24px;
    line-height: 41px;
    text-align: center;
    margin: 0;

    @media all and (max-width: $mobile-breakpoint) {
        font-size: 22px;
        line-height: 1.2;
    }
}

.subHeading {
    font-weight: normal;
    font-size: 16px;
    line-height: 17px;
    text-align: center;
    color: #8E8E8E;
    margin: 4px 0 0 0;

    @media all and (max-width: $mobile-breakpoint) {
        line-height: 1.3;
        margin: 8px 0 0 0;
    }
}

.integrations {
    width: 100%;
    margin: 37px 0 0 0;

    @media all and (max-width: $mobile-breakpoint) {
        margin: 20px 0 0 0;
    }
}

.modalContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10000;
    background-color: rgba(0,0,0,.7);
    animation: revealBg .6s ease forwards;

    @media all and (max-width: $mobile-breakpoint) {
        overflow: hidden;
        padding: 0;
        animation: none;
        opacity: 1;
        background-color: rgba(0,0,0,0);
    }
}

.modal {
    width: 507px;
    max-width: 100%;
    background-color: #fff;
    border-radius: 8px;
    position: relative;
    padding: 24px 35px;
    margin: auto;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.2);
    opacity: 0;
    animation: revealModal .5s ease forwards;
    animation-delay: .15s;

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
        height: 100%;
        overflow: auto;
        border-radius: 0;
        padding: 24px 16px;
    }
}

.closeModalBtn,
a.closeModalBtn{
    width: 72px;
    height: 72px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #333;

    &:hover svg {
        transform: rotate(90deg);
    }

    &:active svg {
        transform: rotate(90deg) scale(.95);
        transition: transform .1s ease;
    }

    svg {
        display: block;
        transform-origin: 50% 50%;
        transition: transform .3s ease;
    }
}

.modalHeader {
    text-align: center;
    margin: 0 0 25px 0;
}

.modalLogo {
    display: block;
    width: 60px;
    height: 60px;
    margin: 0 auto 16px auto;
}

.modalTitle {
    font-weight: bold;
    font-size: 24px;
    line-height: 41px;
    margin: 0;
}

.modalSubTitle {
    font-size: 16px;
    line-height: 17px;
    color: #8E8E8E;
    margin: 4px 0 0 0;
}

.playButton,
a.playButton{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 44px;
    padding: 0 16px 0 12px;
    font-size: 16px;
    line-height: 16px;
    color: #007AFF;
    background-color: #F2F2F2;
    border-radius: 100px;
    margin: 16px auto 0 auto;
    transition: box-shadow .3s ease, transform .3s ease;

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 10px 10px -6px rgba(0, 0, 0, .12);
    }

    &:active {
        transform: translateY(-2px) scale(.95);
        box-shadow: none;
        transition: box-shadow .1s ease, transform .1s ease;
    }
}

.iconPlay {
    display: block;
    width: 22px;
    height: auto;
    margin: 0 10px 0 0;
}

.videoModalContainer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10000;
    background-color: #000;
    padding: 75px;
    overflow: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: revealBg .6s ease forwards;

    @media all and (max-width: $mobile-breakpoint) {
        padding: 35px;
    }
}

.videoModal {
    width: 800px;
    max-width: 100%;
    margin: auto;
    position: relative;
    opacity: 0;
    animation: revealModal .5s ease forwards;
    animation-delay: .15s;
}

.closeVideoModalBtn,
a.closeVideoModalBtn{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -25px;
    top: -25px;
    z-index: 10;
    color: #333;
    transition: box-shadow .3s ease, transform .3s ease;

    &:hover {
        transform: translateY(-2px) rotate(90deg);
        box-shadow: 0 10px 10px -6px rgba(0, 0, 0, .24);
    }

    &:active {
        transform: translateY(-2px) rotate(90deg) scale(.95);
        box-shadow: none;
        transition: box-shadow .1s ease, transform .1s ease;
    }
}

.videoModalFrame {
    width: 100%;
    height: 450px;
    overflow: hidden;
    border-radius: 10px;
    background-color: #000;
    box-shadow: 0 0 60px rgba(0,0,0,.5);

    @media all and (max-width: $mobile-breakpoint) {
        height: 280px;
    }

    iframe {
        display: block;
        width: 100%;
        height: 100%;
    }
}

.videoModalTitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    padding: 16px;
    text-align: center;
    color: #fafafa;
    margin: 0;

    @media all and (max-width: $mobile-breakpoint) {
        font-size: 20px;
    }
}

@keyframes revealBg {
    0% {
        background-color: rgba(0,0,0,0);
    }
    100% {
        background-color: rgba(0,0,0,.7);
    }
}

@keyframes revealModal {
    0% {
        transform: scale(.9);
        opacity: 0;
    }
    100% {
        transform: none;
        opacity: 1;
    }
}
