@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

$color: #000;
$selectedColor: #007aff;

.root {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 8px 16px;
    color: $color;
    font-size: 16px;
    line-height: 22px;
    cursor: pointer;

    &:last-child {
        margin: 0;
    }
}

.prefix {
    display: flex;
    flex: 0 0 36px;
}

.active {
    color: $selectedColor;
    // font-weight: 600;
}

.avatar {
    display: block;
    border-radius: 50%;
    overflow: hidden;

    .active & {
        box-shadow: 0 0 0 2px $selectedColor;
    }
}

.label {
    margin: 0 16px 0 0;
    flex: 1;
    @include crop();
}

.count {
    display: block;
    font-size: 16px;
    line-height: 22px;
    color: #8e8e8e;
    flex: 0 0 auto;

    .active & {
        color: $selectedColor;
    }
}
