.root {
    font-size: 13px;
    line-height: 18px;
    color: #8e8e93;

    ol {
        padding-left: 15px;

        > li {
            margin-bottom: 10px;
        }
    }

    table {
        th {
            font-weight: normal;
            text-align: left;
            padding-right: 10px;
        }

        td {
            color: #000;
            font-weight: bold;
        }
    }
}
