.root {
    height: 100%;
}

.noLinksMessage {
    margin: 0 0 30px 0;
    font-size: 16px;
    line-height: 22px;
    color: #8e8e93;
    text-align: center;
}

.fullWidth {
    width: 100%;
}

.linksContainer {
    margin: 30px 0 0 0;

    > h3 {
        font-size: 18px;
        line-height: 24px;
        font-weight: normal;
        margin: 0 0 16px 0;
    }

    > ul {
        list-style: none;
        margin: 0;
        padding: 0;

        > li {
            margin: 0 0 20px 0;

            &:last-of-type {
                margin: 0;
            }

            .title {
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                display: block;
                margin: 0 0 6px 0;
            }

            .value {
                display: block;
                padding: 11px 16px;
                border-radius: 4px;
                background-color: #f5f5f5;
                font-size: 16px;
                line-height: 22px;
            }
        }
    }
}

.groupedElements {
    display: flex;
    justify-content: space-between;
}

.actions {
    margin: -7px 0 0 0;
}
