.root {
    margin: 0 0 20px 0;

    &:last-child {
        margin: 0;
    }
}

.heading {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    margin: 0 0 10px 0;
}

.buttonsContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.button {
    display: block;
    width: calc(50% - 7px);
    background-color: #f5f5f5;
    border-radius: 8px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: darken(#f5f5f5, 5%);
    }
}
