@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: block;
}

.integration {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    height: 92px;
    background-color: #fff;
    font-size: 24px;
    font-weight: bold;
    line-height: 28px;
    text-decoration: none;
    color: #333;
    padding: 0 23px;
    border-radius: 10px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16);
    margin: 0 0 24px 0;

    @media all and (max-width: $mobile-breakpoint) {
        margin: 0 0 10px 0;
        padding: 0 10px;
        border-radius: 10px;
        height: 60px;
    }
}

.labelsContainer {
    @include crop();
    flex-grow: 1;
}

.label {
    @include crop();
    flex-grow: 1;

    @media all and (max-width: $mobile-breakpoint) {
        font-size: 18px;
        line-height: 1.3;
    }
}

.status {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #3FB990;
    margin: 3px 0 0 0;
}

.logo {
    display: block;
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    margin-right: 16px;

    @media all and (max-width: $mobile-breakpoint) {
        width: 40px;
        height: 40px;
        margin-right: 10px;
    }
}

.button,
a.button{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    min-width: 86px;
    height: 32px;
    padding: 0 16px;
    background-color: #007AFF;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    text-align: center;
    border-radius: 100px;
    cursor: pointer;
    transition: box-shadow .3s ease, transform .3s ease;

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 10px 10px -6px rgba(0, 0, 0, .24);
    }

    &:active {
        transform: translateY(-2px) scale(.95);
        box-shadow: none;
        transition: box-shadow .1s ease, transform .1s ease;
    }

    &.red {
        background-color: #DF4D40;
    }

    &.isFetching {
        pointer-events: none;
    }
}

.soonLabel {
    background-color: #F2F2F2;
    color: #333;
    pointer-events: none;
}

.spinner {
     padding: 0;
 }
