.root {
    margin: 10px 0 0 0;
}

.messengersList {
    list-style: none;
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 32px;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 0.02px;

    &::after {
        content: '';
        display: table;
        height: 0;
        clear: both;
    }

    > li {
        width: 100%;
        margin: 0 0 8px 0;

        &:only-of-type {
            margin: 0;
        }
    }

    /* Two buttons */
    &[count='2'] {
        > li {
            width: calc(50% - 4px);
            float: left;
            margin: 0;

            &:first-of-type {
                margin: 0 8px 0 0;
            }
        }
    }

    /* Three buttons */
    &[count='3'] {
        > li {
            width: calc(33.333333% - 5.5px);
            float: left;
            margin: 0 8px 0 0;

            &:last-of-type {
                margin: 0;
            }

            > a > span {
                display: none;
            }
        }
    }

    /* Four buttons */
    &[count='4'] {
        > li {
            width: calc(33.333333% - 5.5px);
            float: left;
            margin: 0 8px 0 0;

            &:last-of-type {
                margin: 0;
            }

            > a > span {
                display: none;
            }

            &:first-of-type {
                width: 100%;
                margin: 0 0 8px 0;

                > a > span {
                    display: inline-block;
                }
            }
        }
    }

    /* Five buttons */
    &[count='5'] {
        > li {
            width: calc(33.333333% - 5.5px);
            float: left;
            margin: 0 8px 0 0;

            &:last-of-type {
                margin: 0;
            }

            > a > span {
                display: none;
            }

            &:nth-of-type(1),
            &:nth-of-type(2) {
                width: calc(50% - 4px);
                float: left;
                margin: 0 8px 8px 0;

                > a > span {
                    display: inline-block;
                }
            }

            &:nth-of-type(2) {
                margin: 0 0 8px 0;
            }
        }
    }

    /* Six buttons */
    &[count='6'] {
        > li {
            width: calc(33.333333% - 5.5px);
            float: left;
            margin: 0 8px 0 0;

            &:last-of-type {
                margin: 0;
            }

            > a > span {
                display: none;
            }

            &:nth-of-type(1),
            &:nth-of-type(2) {
                margin: 0 8px 8px 0;
            }

            &:nth-of-type(3) {
                margin: 0 0 8px 0;
            }
        }
    }
}
