@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    position: fixed;
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
}

.html {
    body {
        overflow: hidden;
    }
}

.container {
    display: flex;
    position: relative;
    width: 100%;
    flex: 1;
    flex-wrap: nowrap;
    flex-direction: row;
    height: 100%;
    overflow: hidden;

    @media all and (max-width: $mobile-breakpoint) {
        flex-direction: column;
    }
}
