.header {
    padding: 0 0 16px 0;
    border-bottom: 1px solid #cdced3;
    margin: 0 0 16px 0;
}

.publishedContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &.priceTogglerContainer {
        margin: 0 0 10px 0;
    }
}

.publishedLabel {
    display: block;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    padding: 0 0 0 16px;
    position: relative;

    &.priceLabel {
        padding: 0;
    }
}

.publishedDot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    left: 0;
    top: calc(50% - 5px);

    &.green {
        background-color: #3fb990;
    }
}

.userLink {
    margin: 16px 0 0 0;
}

.photosContainer {
    margin: 0 0 10px 0;

    .imageUploadContainer {
        position: relative;
    }
}

.removeBtnContainer {
    text-align: center;
    margin: 30px 0 0 0;
}

.imagesBlock {
    width: 100%;
}

.imagesList {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
}

.imagePreview {
    width: calc(33% - 10px);
    margin: 0 16px 6px 0;
    padding: 10px 0 0 0;
    position: relative;
    list-style: none;
    cursor: grab;
    pointer-events: auto !important;

    &.sortable {
        cursor: grabbing;
    }

    &:nth-of-type(3n) {
        margin: 0 0 6px 0;
    }

    .imagePreviewContainer {
        width: 100%;
        padding-top: 100%;
        position: relative;

        > img {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 4px;
        }
    }
}

.removePhotoBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #f9f9f9;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: -10px;
    box-shadow: inset 0 0 0 1px #ccc;

    > svg {
        transform: rotate(45deg);
        pointer-events: none;
    }
}

.sectionLabel {
    display: block;
    color: #000;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 0;
}

.addPhotoBtn {
    width: 100%;
    padding-top: 100%;
    position: relative;
    background-color: #e9e9e9;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;
}

.addPhotoBtnInput {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: pointer;
    opacity: 0;
    z-index: 1;
}

.addPhotoBtnContent {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.photosError {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    padding: 0;
    margin: 6px 0 0;
    list-style-type: none;
    color: #dc5960;
}
