.root {
    height: 100%;
}
.heading {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 6px 0;
    //padding: 0 0 0 28px;

    > img {
        display: block;
        width: 20px;
        height: auto;
        position: absolute;
        top: 0;
        left: 0;
    }
}

.bio {
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 30px 0;

    > p {
        font-size: 14px;
        line-height: 17px;
        color: rgba(0, 0, 0, 0.6);
        margin: 0 0 15px 0;
    }

    .tip {
        border-radius: 4px;
        background-color: rgba(0, 122, 255, 0.05);
        padding: 10px 16px;
        margin: 6px 0 0 0;
        font-size: 14px;
        line-height: 20px;

        > p {
            margin: 0;
        }
    }
}

.qrImage {
    width: 300px;
    max-width: 100%;
    margin: -15px auto 0 auto;
    text-align: center;

    > img {
        display: block;
        width: 100%;
        height: auto;
        margin-top: 20px;
    }
}

.shareList {
    list-style: none;
    margin: 0 auto;
    width: 70%;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;

    > li {
        > a {
            font-size: 21px;
            color: white;
            border-radius: 40px;
            text-align: center;
            padding: 10px 4px 6px 10px;
            display: inline-block;

            > svg {
                margin: -1px 6px 0 0;
            }

            &.twitter {
                background-color: #2aa3ef;
            }

            &.facebook {
                background-color: #3a589b;
            }

            &.vkontakte {
                background-color: #4a7298;
            }

            &.instagram {
                background: radial-gradient(
                    circle at 30% 107%,
                    #fdf497 0%,
                    #fdf497 5%,
                    #fd5949 45%,
                    #d6249f 60%,
                    #285aeb 90%
                );
            }
        }
    }
}

.widgetSection {
    text-align: center;
}

.section {
    margin: 0 0 15px 0;
}

.label {
    font-size: 16px;
    font-weight: normal;
    color: #8e8e93;
    line-height: 22px;
    display: block;
    margin: 0 0 10px 0;

    &.sectionHeading {
        margin: 0 0 16px 0;
    }
}

.codeBlock {
    overflow: hidden;
    position: relative;
    padding: 11px 16px;
    border-radius: 4px;
    background-color: #f5f5f5;
    font-size: 16px;
    line-height: 22px;
    word-break: break-all;
    cursor: pointer;
    text-align: left;

    &::after {
        content: attr(data-success);
        display: flex;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        background-color: #3eb991;
        font-weight: 500;
        color: #fff;
        opacity: 0;
        pointer-events: none;
        transform: scale(0.9);
        transition: transform 0.3s ease, opacity 0.3s ease;
    }

    &.copied::after {
        opacity: 1;
        transform: scale(1);
        pointer-events: auto;
    }
}

.codeBlock::selection {
    background: rgba(0, 0, 0, 0);
}

.tipCode {
    font-size: 13px;
    line-height: 18px;
    margin: 16px 0 0 0;
    white-space: pre-line;
    text-align: left;
}

.sectionBlock {
    display: block;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    padding: 16px;
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.16);
    margin-bottom: 16px;
    border-radius: 10px;
}
.headingTitle {
    font-size: 20px;
    color: #000;
    text-align: left;
    font-weight: 500;
    display: inline-block;
    margin-left: 12px;
    margin-bottom: 12px;
    margin-top: 0px;
}
.headingInstruction {
    display: block;
    line-height: 17px;
    text-align: left;
    font-size: 14px;
    color: #8e8e8e;
    margin: 0px;
    font-weight: 400;
}

.userLink {
    margin-bottom: 8px;
}

.instagramTipImg {
    display: block;
    width: 280px;
    max-width: 100%;
    margin: 22px auto;
    border-radius: 4px;
}

.instagramSettingsLink {
    text-align: center;
    font-size: 14px;
    line-height: 17px;
}

.socialsTab {
    text-align: center;
}
.chevronRight {
    position: absolute;
    right: 16px;
    top: 36px;
    fill: black;
}
.mainIcon {
    position: relative;
    max-width: 20px;
    max-height: 20px;
    top: 1px;
}
.Badge {
    position: relative;
    top: -4px;
    margin-left: 6px;
}
.modalDialog {
    border-radius: 10px;
    margin-left: -20px;
    margin-top: -40px;
    margin-bottom: -30px;
    width: calc(100% + 40px);
    box-sizing: border-box;
    text-align: center;
    font-size: 18px;
    font-weight: 500;
}
.modalDialog .header {
    background: #2eb67c;
    color: white;
    border-radius: 10px 10px 0 0;
    padding-top: 16px;
    padding-bottom: 16px;
}
.modalDialog p {
    margin-top: 24px;
    margin-bottom: 24px;
}
.modalDialog a {
    display: inline-block;
    text-decoration: none;
    color: white;
    background: #007aff;
    border-radius: 30px;
    padding: 16px;
    width: 90%;
    margin-bottom: 30px;
}
