@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    width: 100%;
    padding: 0 0 16px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #C7C7CC;
    margin-bottom: 13px;

    &:last-child {
        margin-bottom: 0;
        border-bottom: none;
        padding-bottom: 0;
    }
}

.left {
    flex: 1;
    width: calc(100% - 40px);
    cursor: pointer;
}

.right {
    width: 40px;
    flex: 0 0 auto;
    color: #333;
    display: flex;
    justify-content: flex-end;
}

.edit {
    color: #999;
    transition: color .3s ease;
    margin-left: auto;

    &:hover {
        color: #333;
    }
}

.shortcut {
    display: block;
    width: 100%;
    font-weight: 600;
    font-size: 13px;
    line-height: 18px;
    margin: 0;
    @include crop();
}

.message {
    width: 100%;
    font-size: 16px;
    line-height: 22px;
    color: #999;
    margin: 4px 0 0 0;
    @include crop();
}
