.backButton {
    display: block;
    width: 50px;
    height: 100%;
    background-image: url('../../../../../img/icon-go-back.svg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    overflow: hidden;

    > * {
        display: block;
        width: 100%;
        height: 100%;
        opacity: 0;
    }
}
