@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: relative;
    font-weight: normal;
    display: flex;
    align-items: center;
    min-height: 50px;
    font-size: 15px;
    padding: 0 20px 0 0;
    color: $font-default-color;
    cursor: pointer;

    &.disabled {
        opacity: 0.6;
        pointer-events: none;
    }
}

.label {
    flex: 1;
    @include crop();
}

.soon {
    pointer-events: none;
}

.icon {
    position: static;
    margin: 0 12px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
}

.badge {
    display: block;
    position: absolute;
    right: 0;
    top: calc(50% - 9px);
    padding: 0 6px;
    border-radius: 100px;
    background-color: #ff9500;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    line-height: 18px;
    color: #fff;
}

.arrow {
    position: absolute;
    right: 0;
    top: calc(50% - 7px);
    transition: color 0.3s ease;
    color: #333;
}

.chevronData {
    display: flex;
    flex-direction: row;
}
