@import '@mssgme/scss-basics/vars';

.root {
    position: relative;
    width: 100%;
    height: 700px;
    max-height: calc(100vh - 40px);
    background-color: #fafafa;

    &.widgetPreview {
        max-height: calc(100vh - 180px);
    }

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
        height: 100%;
        box-shadow: none;
        border-radius: 0;
        max-height: inherit;
    }

    .inner {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow-y: scroll;
        //padding: 0 0 24px 0;
        -webkit-overflow-scrolling: touch;
    }

    .inner::-webkit-scrollbar {
        width: 0;
    }

    .inner::-webkit-scrollbar-track {
        background: rgba(255, 255, 255, 0);
    }

    .inner::-webkit-scrollbar-thumb {
        background: rgba(255, 255, 255, 0);
        transition: background 0.3s ease;
    }

    .inner:hover::-webkit-scrollbar-thumb {
        background: rgba(200, 200, 200, 0.5);
    }

    .background {
        position: static;
        width: 100%;
        height: 200px;
    }

    .slideUp {
        transform: translateY(-100px);
        opacity: 0;
    }

    .slideDown {
        transform: translateY(100px);
        opacity: 0;
    }

    .animated {
        opacity: 1;
        transform: translateY(0);
        transition: transform 1s ease, opacity 1s ease;
    }
}

.cardContent {
    //padding: 0 16px;
}

.footerMessengers {
    padding: 16px;
    position: absolute;
    width: 100%;
    background-color: #fff;
    z-index: 10;
    bottom: 0;
    left: 0;
    border-top: 1px solid #eee;
    transform: translateY(100%);
    transition: transform 0.6s ease;

    > div {
        margin: 0;
    }

    &.visible {
        transform: none;
    }
}

.overlayBg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    z-index: 15;
    pointer-events: none;
    transition: opacity 0.3s ease;

    &.visible {
        opacity: 1;
        pointer-events: auto;
        z-index: 10;
    }
}
