@import '@mssgme/scss-basics/vars';

.noaccess {
    padding: 30px;
}

.section {
    padding: 30px;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);

    @media all and (max-width: $tablet-portrait-breakpoint) {
        padding: 20px;
    }

    &:last-of-type {
        box-shadow: none;
    }
}

.label {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    display: block;
    margin: 0 0 10px 0;

    &.sectionHeading {
        margin: 0 0 16px 0;
    }
}

.formField {
    margin: 0 0 30px 0;

    &:last-of-type {
        margin: 0;
    }
}

.codeBlock {
    overflow: hidden;
    position: relative;
    padding: 11px 16px;
    border-radius: 4px;
    background-color: #f5f5f5;
    font-size: 16px;
    line-height: 22px;
    word-break: break-all;
    cursor: pointer;

    &::after {
        content: attr(data-success);
        display: flex;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        background-color: #3eb991;
        font-weight: 500;
        color: #fff;
        opacity: 0;
        pointer-events: none;
        transform: scale(0.9);
        transition: transform 0.3s ease, opacity 0.3s ease;
    }

    &.copied::after {
        opacity: 1;
        transform: scale(1);
        pointer-events: auto;
    }
}

.codeBlock::selection {
    background: rgba(0, 0, 0, 0);
}

.tip {
    font-size: 13px;
    line-height: 18px;
    margin: 16px 0 0 0;
    white-space: pre-line;
}
