@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    background-color: #fafafa;
}

.list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.listItem {
    list-style: none;
    padding: 0;
    margin: 0 0 10px 0;
    display: flex;
    width: 100%;

    > a {
        max-width: calc(100% - 37px);
        flex: 1;
        position: relative;
        font-weight: normal;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: $font-default-color;
        padding: 10px 14px;
        background-color: #f5f5f5;
        border-radius: 10px 0 0 10px;
        transition: color .3s ease;

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover {
                color: $link-color;
            }
        }

        .label {
            flex: 1;
            @include crop();
        }
    }
}

.status {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ccc;
    margin: 0 10px 0 0;
    pointer-events: none;

    &.active {
        background-color: #3fb990;
    }
}

.label {
    display: block;
    flex: 1;
    @include crop();
}

.sortable {
    display: flex;
    box-shadow: 0 0 30px -15px rgba(0,0,0,.1);
    z-index: 1;
    pointer-events: auto !important;
    cursor: grabbing;

    .dragHandle,
    a {
        cursor: grabbing;
    }
}

.sortableList {
    transition: opacity .3s ease;

    &.isFetching {
        opacity: .6;
        pointer-events: none;
    }
}

.menuHeader {
    display: flex;
    align-items: center;
}

.heading {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin: 0;
}

.addLink {
    margin-left: auto;
    margin-right: -8px;
}

.headerBtns {
    display: flex;
    align-items: center;
}

.headerLink {
    display: flex;
    align-items: center;
    height: 34px;
    margin: 0 0 0 10px;
}

.dragHandle {
    width: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    border-radius: 0 10px 10px 0;
    border-left: 2px solid #fff;
    cursor: grab;

    svg {
        pointer-events: none;
        transition: color .3s ease;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            svg {
                color: $link-color;
            }
        }
    }
}

.noCollectionsMessage {
    padding: 70px 0;
    max-width: 225px;
    margin: 0 auto;
    text-align: center;

    > h3 {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        margin: 0;
    }

    > p {
        font-size: 14px;
        line-height: 19px;
        margin: 12px 0 0 0;
    }
}
