.root {
    padding: 0 12px 8px 40px;
    margin: 0 0 8px 0;
    position: relative;
    min-height: 40px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    &:last-of-type {
        border: none;
        margin: 0;
    }
}

.avatarContainer {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #f5f5f5;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.commentContent {
    width: 250px;
    max-width: 100%;
}

.name {
    display: block;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
}

.date {
    display: block;
    position: relative;
    font-size: 12px;
    line-height: 18px;
    color: #8e8e93;
    padding: 0 0 0 12px;

    &.green::before {
        content: '';
        display: block;
        box-sizing: border-box;
        width: 8px;
        height: 6px;
        border-style: solid;
        border-width: 0 4px 6px 4px;
        border-color: transparent transparent #3eb990 transparent;
        position: absolute;
        left: 0;
        top: calc(50% - 4px);
    }

    &.red::before {
        content: '';
        display: block;
        box-sizing: border-box;
        width: 8px;
        height: 6px;
        border-style: solid;
        border-width: 6px 4px 0 4px;
        border-color: #ec565d transparent transparent transparent;
        position: absolute;
        left: 0;
        top: calc(50% - 4px);
    }
}

.comment {
    font-size: 13px;
    line-height: 18px;
    word-break: break-word;
    margin: 0;
    padding: 0;
}

.deleteButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    position: absolute;
    top: -8px;
    right: 0;
    cursor: pointer;

    svg {
        display: block;
        transition: color 0.3s ease;
    }

    &:hover {
        svg {
            color: #dc5960 !important;
        }
    }
}
