@import '@mssgme/scss-basics/mixins/crop';

.noLinksMessage {
    margin: 0 0 30px 0;
    font-size: 16px;
    line-height: 22px;
    color: #8e8e93;
    text-align: center;
}

.fullWidth {
    width: 100%;
    min-height: 44px;
}

.linksContainer {
    margin: 0 0 30px 0;

    > h3 {
        font-size: 18px;
        line-height: 24px;
        font-weight: normal;
        margin: 0 0 16px 0;
    }
}

.link {
    margin: 0 0 20px 0;

    &:last-of-type {
        margin: 0;
    }

    .title {
        @include crop();
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        display: block;
        margin: 0 0 6px 0;
    }

    .value {
        color: #000;
    }
}
