.root {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 284px;
    margin: 10px 0 0 0;
    background-color: #fff;
    border-radius: 4px;

    @media all and (max-width: 340px) {
        max-width: 260px;
    }
}

.hueContainer {
    position: relative;
    width: calc(100% - 85px);
    height: 32px;
}

.inputContainer {
    width: 85px;
    padding: 0 0 0 10px;
    height: 32px;

    input {
        font-family: 'Inter', sans-serif;
        font-weight: 500;
        line-height: 32px;
        color: #fff;
        text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
        text-align: center;
        display: block;
        width: 100%;
        height: 100%;
        padding: 0 10px;
        outline: none;
        border-radius: 6px;
        box-shadow: none;
        border: 1px solid transparent;
        transition: background-color 0.1s linear, border-color 0.1s linear;

        &.invalidInput {
            background-color: transparent !important;
            border-color: red !important;
            color: #000;
        }
    }

    input::-webkit-input-placeholder {
        color: #ccc;
    }
    input::-moz-placeholder {
        color: #ccc;
    }
    input:-ms-input-placeholder {
        color: #ccc;
    }
    input:-moz-placeholder {
        color: #ccc;
    }
    input.invalidInput::-webkit-input-placeholder {
        color: #999;
        text-shadow: none;
    }
    input.invalidInput::-moz-placeholder {
        color: #999;
        text-shadow: none;
    }
    input.invalidInput:-ms-input-placeholder {
        color: #999;
        text-shadow: none;
    }
    input.invalidInput:-moz-placeholder {
        color: #999;
        text-shadow: none;
    }
}
