@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

$width: 375px;

.root {
    position: relative;
    flex: 0 $width;
    height: 100%;
    background-color: #fff;
    border-left: 1px solid #f2f2f2;
    overflow-x: hidden;

    @media all and (max-width: 1024px) {
        flex: 1;
    }
}

.content {
    padding: 13px 24px;
}

.header {
    padding: 42px 24px 13px 24px;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.sectionHeading {
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    margin: 0 0 9px 0;
}

.avatar {
}

.userName {
    flex: 1;
    margin: 8px 0 0 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
    width: 100%;
    text-align: center;
    @include crop();
}

.infoList {
    list-style: none;
    margin: 0;
    padding: 0;

    > li:not(:last-child) {
        margin: 0 0 2px 0;
    }
}

.nav {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding: 10px;
    height: 68px;
    display: flex;
    z-index: 1;
}
