.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: rgba(255,255,255,.8);
}

.enter {
    opacity: 0;
    &Active {
        opacity: 1;
        transition: opacity 0.6s;
    }
}

.exit {
    opacity: 1;
    &Active {
        opacity: 0;
        transition: opacity 0.6s;
    }
}
