.root {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: #e9e9e9 50% 50%;
    background-size: cover;
    flex-shrink: 0;

    .spinner {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
    }

    &.rounded {
        border-radius: 4px;

        .spinner {
            border-radius: 4px;
        }
    }

    .emptyTip {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        > span {
            display: block;
            width: 100%;
            font-size: 15px;
            line-height: 18px;
            text-align: center;
            margin: 8px 0 0 0;
        }
    }

    .noUrl {
        pointer-events: none;
    }
}
