.tag {
    background-color: #ededf0;
    border-radius: 4px;
    color: #000;
    display: inline-block;
    font-size: 13px;
    font-weight: normal;
    margin-bottom: 5px;
    margin-right: 5px;
    padding: 4px 10px;
    line-height: 18px;

    &Remove {
        cursor: pointer;
        font-weight: bold;
        color: #000;
        text-align: center;
        width: 12px;
        height: 12px;
        border-radius: 1px;
        display: inline-block;
        margin-left: 4px;
        line-height: 12px;

        &::before {
            content: '';
        }

        &:hover {
            background-color: #b4b4bb;
        }
    }
}

.input {
    background: transparent;
    border: 0;
    font-size: 16px;
    line-height: 19px;
    color: #000;
    outline: none;
    padding: 6px;
    width: 100%;
    cursor: pointer;

    &::placeholder {
        color: #007aff;
    }

    &:focus {
        cursor: text;

        &::placeholder {
            color: transparent;
        }
    }
}
