.root {
    display: flex;
    flex-direction: row;
}

.item {
    margin-left: 10px;

    &:first-child {
        margin-left: 0;
    }
}

.center {
    justify-content: center;
}

.right {
    justify-content: flex-end;
}

.left {
    justify-content: flex-start;
}

.bordered {
    padding: 6px;
    border-radius: 4px;
    border: 1px solid #d1d1d6;
}
