@import '@mssgme/scss-basics/vars';

.root {
    display: inline-block;
    padding: 4px 6px;
    font-size: 12px;
    line-height: 16px;
    font-weight: normal;
    color: $background-color;
    border-radius: 4px;
    min-height: 20px;
}

.dot {
    min-height: unset;
    min-width: 8px;
    width: 8px;
    height: 8px;
    padding: 0;
    line-height: 0;
    border-radius: 100%;
}

.primary {
    background-color: $primary;
}

.warning {
    background-color: $warning;
}

.danger {
    background-color: $danger;
}

.success {
    background-color: $success;
}

.flat {
    background-color: $font-default-color;
    color: $background-color;
}

.inverse {
    background-color: $background-color;

    &.primary {
        color: $primary;
    }

    &.warning {
        color: $warning;
    }

    &.danger {
        color: $danger;
    }

    &.success {
        color: $success;
    }
}
