@import '@mssgme/scss-basics/mixins/crop';

.root {
    color: #2b303a;
    overflow: hidden;
}

.label {
    flex: 1;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: #000;
    max-width: 200px;
    @include crop();
}

.arrow {
    flex: 0 0 auto;
    margin-left: 2px;
    transition: transform 0.6s;
    transform: rotate(0deg);

    .root[ariaPressed='true'] & {
        transform: rotate(180deg);
    }
}
