@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.listItem {
    width: 100%;
    list-style: none;
    margin: 0 0 20px 0;

    > img {
        display: inline-block;
        vertical-align: top;
        margin: 0 6px 6px 0;
    }
}
.messengerPane {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
}
.messengerLink {
    display: block;
    color: #000;
    background: #fff;
    min-height: 48px;
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
    padding: 4px;
    text-overflow: unset;
    flex: 1;

    span {
        color: #333333;
        font-size: 14px;
        font-weight: 400;
        margin: 0px 0px 0px 16px;
        display: inline-block;
        vertical-align: middle;
        width: calc(100% - 59px);
        @include crop();
    }

    img {
        display: inline-block;
        vertical-align: middle;
        width: 40px;
        height: 40px;
    }
}
.messengerLinkActive {
    background: #f2f2f2;
    width: calc(100% - 39px);
}
.messengerLabel {
    display: inline-block;
    vertical-align: top;
    margin: 0 0 6px 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
}
.dragHandle {
    flex-basis: 37px;
    min-width: 37px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 2px solid #fff;
    cursor: grab;
    background-color: #f2f2f2;
    border-radius: 0 8px 8px 0;

    svg {
        pointer-events: none;
        transition: color 0.3s ease;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            svg {
                color: $link-color;
            }
        }
    }
}
.button {
    margin-top: 8px;
    min-width: 93px;
    flex-shrink: 0;
}
