@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: block;
    width: 100%;
}

.fetching {
    pointer-events: none;
}

.list {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
}

.listItem {
    list-style: none;
    padding: 0;
    margin: 0 0 8px 0;
    display: flex;
    width: 100%;
    background-color: #fff;

    > a {
        max-width: calc(100% - 40px);
        flex: 1;
        position: relative;
        font-weight: normal;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: $font-default-color;
        padding: 14px 8px 14px 16px;
        transition: color .3s ease;

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover {
                color: $link-color;
            }
        }

        .label {
            flex: 1;
            @include crop();
        }
    }
}

.hiddenItem {
    display: none;
}

.status {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ccc;
    margin: 0 10px 0 0;
    pointer-events: none;

    &.active {
        background-color: #3fb990;
    }
}

.label {
    display: block;
    flex: 1;
    @include crop();
}

.sortable {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    box-shadow: 0 0 20px rgba(0,0,0,.15);
    z-index: 1;
    pointer-events: auto !important;
    cursor: grabbing;

    .dragHandle,
    a {
        cursor: grabbing;
    }
}

.sortableList {
    transition: opacity .3s ease;

    &.isFetching {
        opacity: .6;
        pointer-events: none;
    }
}

.dragHandle {
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: grab;

    svg {
        pointer-events: none;
        transition: color .3s ease;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            svg {
                color: $link-color;
            }
        }
    }
}

.counter {
    font-size: 13px;
    color: #8E8E8E;
}
