.portal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#000, 0.2);
    z-index: 999;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    animation-name: reveal;
    animation-timing-function: ease;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.root {
    position: relative;
    width: 335px;
    max-width: 100%;
    background-color: #fff;
    padding: 30px 20px;
    border-radius: 10px;
    box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.08);
    opacity: 0;
    transform: translateY(50px);
    animation-name: revealUp;
    animation-timing-function: ease;
    animation-duration: 1s;
    animation-delay: 0.2s;
    animation-fill-mode: forwards;
}

.small {
    width: 335px;
}

.medium {
    width: 600px;
}

.large {
    width: 1200px;
}

@keyframes reveal {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes revealUp {
    0% {
        opacity: 0;
        transform: translateY(50px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
