.root {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #f2f2f2;
}

.settings {
    path {
        transition: transform 0.3s ease;
    }

    &:hover {
        path:nth-child(2) {
            transform: translateX(-4px);
        }

        path:nth-child(4) {
            transform: translateX(4px);
        }
    }
}

.close {
    svg {
        transition: transform 0.3s ease;
    }

    &:hover svg {
        transform: rotate(90deg);
    }
}
