.root {
    position: relative;
}

.dropdown {
    position: absolute;
    background-color: #ffffff;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    padding: 8px 0;
    z-index: 2;
    width: 248px;
    max-height: 400px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
