.root {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: background-color .6s ease;
    pointer-events: none;
    background-color: rgba(0,0,0,0);
    z-index: 1000;

    &.visible {
        pointer-events: auto;
        background-color: rgba(255,255,255,.8);
    }

    @media all and (min-width: 768px) {
        display: none;
    }
}

.menuContainer {
    width: 300px;
    max-width: calc(100% - 75px);
    height: 100%;
    background-color: #fafafa;
    transform: translateX(-100%);
    transition: transform .6s ease, box-shadow .6s ease;
    position: relative;
    z-index: 1;

    &.visible {
        box-shadow: 4px 0 16px rgba(0,0,0,.08);
        transform: none;
    }
}

.contentBox {
    width: 100%;
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
}

.contentBoxTop {
    width: 100%;
    flex: 1;
    flex-shrink: 0;
    padding: 16px;
}

.logoContainer {
    padding: 8px 16px 36px 16px;
}

.logo {
    display: block;
    width: 36px;
    height: auto;
    margin: 0 auto;
}

.navList {
    display: block;
    list-style: none;
    margin: 0;
    padding: 0;
}

.navListItemLink {
    display: flex;
    width: 100%;
    height: 46px;
    padding: 0 8px;
    align-items: center;
    color: #000;
    margin: 0 0 10px 0;
    position: relative;
    transition: color .3s ease, background-color .3s ease;

    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: #F2F2F2;
        position: absolute;
        bottom: -6px;
        left: 0;
    }

    &.active {
        background-color: #000;
        border-radius: 4px;
        color: #fff;
    }

    &.new::before {
        content: '';
        display: block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #F14941;
        position: absolute;
        top: 8px;
        left: 23px;
        border: 2px solid #fafafa;
    }
}

.navLinkIcon {
    display: block;
    width: 23px;
    height: auto;
    margin: 0 13px 0 0;
}

.navLinkTitle {
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
}

.navLinkLabel {
    display: block;
    padding: 3px 8px;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    border-radius: 100px;
    margin-left: auto;
    background-color: #F2F2F2;
    color: #8E8E8E;

    &.red {
        background-color: #F5515F;
        background-image: linear-gradient(180deg, #F5515F 0%, #D81330 97.35%);
        color: #fff;
    }
}

.subLayer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.contentBoxBottom {
    margin-top: auto;
    padding: 0 16px 6px 16px;

    .navListItemLink::after {
        display: none;
    }
}
