.root {
    position: relative;
}

.blockedContent {
    filter: saturate(50%) contrast(50%);
    opacity: 0.6;
}

.blockedOverlay {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.striped .blockedContent {
    $stripe1: rgba(200, 200, 200, 0.2);
    $stripe2: transparent;
    $size: 16px;
    background-image: linear-gradient(
            45deg,
            $stripe2 25%,
            $stripe1 25%,
            $stripe1 50%,
            $stripe2 50%,
            $stripe2 75%,
            $stripe1 75%,
            $stripe1 100%
    );
    background-size: $size $size;
    //border-radius: 16px;
}

.blockedWrapper {
    padding: 8px;
    border-radius: 8px;
    background: #fff8;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.dashed {
    margin: 0 16px;
    padding: 0;

    .blockedContent {
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='8' ry='8' stroke='%23c7c7c7' stroke-width='2' stroke-dasharray='10 8' stroke-dashoffset='7' stroke-linecap='butt'/%3e%3c/svg%3e");
        //border: 2px dashed #c7c7c7;
    }

    .wrappedContent {
        margin: 0 -16px;
        flex: 1;
    }

    .blockedOverlay {
        padding: 0;
    }

    .blockedWrapper {
        width: 100%;
        height: 100%;
    }
}
