.root {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    overflow: hidden;
    animation: opacity 0.6s ease forwards;
}

@keyframes opacity {
    0% {
        background-color: rgba(0, 0, 0, 0);
    }
    100% {
        background-color: rgba(0, 0, 0, 0.4);
    }
}

@keyframes reveal {
    0% {
        transform: translateY(100px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.pane {
    width: calc(100% - 40px);
    border-radius: 10px;
    background-color: #fff;
    padding: 26px 16px 16px 16px;
    text-align: center;
    position: relative;
    animation: reveal 0.6s ease forwards;

    > h1 {
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        text-align: center;
        margin: 0;
        padding: 0 20px;
    }

    > p {
        font-size: 14px;
        line-height: 20px;
        white-space: pre-wrap;
        margin: 0 0 10px 0;
    }

    > button {
        margin: 16px 5px 0 5px;

        &:only-of-type {
            margin: 16px 0 0 0;
        }
    }
}

.closeButton {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    transition: transform 0.6s ease;

    &:hover {
        transform: rotate(-90deg);
    }
}
