$selected-color: #2979FF;

.root {
    border-bottom: 1px solid #EDEDF0;
    display: flex;
    margin: 16px;
}

.label {
    position: relative;
    margin-left: 30px;
    margin-bottom: -1px;

    &:first-child {
        margin-left: 0;
    }
}

.text {
    display: block;
    position: relative;
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 8px;
    color: black;
    cursor: pointer;
    transition: color 0.3s;
}

.input {
    position: absolute;
    visibility: hidden;

    &:checked + span {
        color: $selected-color;
        font-weight: bold;
        border-bottom: 2px solid $selected-color;
    }
}
