@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: relative;
    width: calc(100% - 32px);
    margin: 16px auto 0 auto;
    transition-delay: 0.3s !important;

    &.slideDown {
        transform: translateY(100px);
        opacity: 0;
    }

    &.animated {
        opacity: 1;
        transform: translateY(0);
        transition: transform 1s ease, opacity 1s ease;
    }
}

.linksList {
    list-style: none;
    padding: 0;
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    //letter-spacing: .02px;

    > li {
        //background-color: #F2F2F2;
        //border-radius: 8px;
        margin: 0 0 8px 0;
        position: relative;
        user-select: none;
        transition: box-shadow 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;

        //@media all and (min-width: $mobile-breakpoint) {
        //    &:hover {
        //        box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
        //        transform: translateY(-2px);
        //    }
        //}

        //&::after {
        //    content: '';
        //    display: block;
        //    width: calc(100% - 32px);
        //    height: 1px;
        //    background-color: #cecece;
        //    position: absolute;
        //    bottom: 0;
        //    left: 16px;
        //    opacity: .5;
        //}

        &:last-of-type {
            margin: 0;

            &::after {
                display: none;
            }
        }

        > a,
        > span {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 8px 20px;
            font-weight: 500;
            color: #000;
            cursor: pointer;
            background-color: #fff;
            border-radius: 10px;
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
            min-height: 46px;
            transition: box-shadow 0.3s ease, transform 0.3s ease;
            @include crop();

            @media all and (min-width: $tablet-landscape-breakpoint) {
                &:hover {
                    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
                    transform: translateY(-2px);
                }
            }
        }
    }
}

.heading {
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #333;
    margin: 0 0 10px 0;
}

.burger {
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 9px;
    margin: 0 0 0 10px;
    position: relative;

    > div {
        width: 100%;
        height: 1px;
        border-radius: 100px;
        background-color: #8d8d8d;
        position: absolute;
        left: 0;

        &:nth-of-type(1) {
            top: 0;
        }

        &:nth-of-type(2) {
            top: 4px;
        }

        &:nth-of-type(3) {
            bottom: 0;
        }
    }
}

.iconLink {
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: auto;
    margin: 0 0 0 10px;
    color: #8d8d8d;
    pointer-events: none;
}

.iconGlobe {
    display: inline-block;
    vertical-align: middle;
    height: auto;
    margin: 0 0 0 10px;
    color: #8d8d8d;
    pointer-events: none;
}

.linkEllipsis {
    @include crop();
    display: inline-block;
    vertical-align: top;
    // max-width: calc(100% - 20px); why?
    pointer-events: none;
}

.linkTip {
    display: block;
    width: 100%;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: #8e8e8e;
}
