@import '@mssgme/scss-basics/mixins/crop';

.scrollBox {
    padding: 10px 16px 16px 16px;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.listItem {
    border-bottom: 1px solid rgba(0,0,0,.1);

    &:last-child {
        border: none;
    }

    > a, > div {
        display: flex;
        width: 100%;
        min-height: 50px;
        align-items: center;
        color: #333;
        cursor: pointer;
    }
}

.listItemName {
    @include crop();
    display: flex;
    width: 50%;
    align-items: center;
    flex-shrink: 0;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.4;
}

.listItemRight {
    width: 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-shrink: 0;
}

.previewImg {
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 50%;
    background-color: #ccc;

    &.square {
        border-radius: 4px;
        background-color: transparent;
    }

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.iconArrow {
    width: 8px;
    height: 100%;
    margin: 0 0 0 12px;
    color: #C7C7CC;
    flex-shrink: 0;
}

.textPreview {
    @include crop();
    font-weight: normal;
    font-size: 14px;
    color: #777;
}
