.root {
    list-style: none;
    margin: 0;
    padding: 0;

    > li {
        max-width: 100%;
        margin: 0 0 8px 0;

        &:last-of-type {
            margin: 0;
        }

        > a {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 36px;
            border-radius: 10px;
            font-weight: normal;
            font-size: 15px;
            color: #fff;
            background-color: #000;
            box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.12);

            > svg {
                margin: -1px 6px 0 0;
            }

            &.messenger {
                background-color: #0284ff;
            }

            &.facebook {
                background-color: #3b5998;
            }

            &.whatsapp {
                background-color: #25d366;
            }

            &.telegram {
                background-color: #0088cc;
            }

            &.viber {
                background-color: #59267c;
            }
        }
    }
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    cursor: pointer;

    svg {
        pointer-events: none;
    }

    &:only-child {
        margin-left: auto;
    }
}
canvas {
    display: none;
}
