@import '@mssgme/scss-basics/vars';

.root {
    position: relative;
    padding: 16px 55px 57px 24px;
    min-height: 168px;

    //@media all and (max-width: $mobile-breakpoint) {
    //    padding: 28px 28px 80px 28px;
    //}

    > h2 {
        position: relative;
        font-weight: 500;
        font-size: 36px;
        line-height: 42px;
        color: #fff;
        text-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
        margin: 0;
    }

    > p {
        position: relative;
        font-size: 16px;
        line-height: 20px;
        color: #fff;
        text-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
        margin: 8px 0 0 0;
        white-space: pre-line;
        transition-delay: 0.1s !important;
    }

    .slideDown {
        transform: translateY(100px);
        opacity: 0;
    }

    .animated {
        opacity: 1;
        transform: translateY(0);
        transition: transform 1s ease, opacity 1s ease;
    }
}

.shareButton {
    position: absolute;
    top: 19px;
    right: 19px;
}
