@import '@mssgme/scss-basics/vars';

.rootContainer {
    font-size: 16px;
    line-height: 1.4;
    word-break: break-word;

    p {
        margin: 0;
    }
}

.list {
    margin: 0;
    padding: 0 0 0 20px;

    > li:not(:last-child) {
        margin: 0 0 12px 0;
    }

    a {
        font-weight: bold;
    }
}

.input {
    display: block;
    width: 100%;
    border-radius: 8px;
    background-color: #F2F2F2;
    padding: 10px;
    font-size: 14px;
    line-height: 16px;
    outline: none;
    margin: 23px 0 0 0;
    border: none;
    box-shadow: none;
    appearance: none;

    @media all and (max-width: $mobile-breakpoint) {
        font-size: 16px;
    }
}

.buttonContainer {
    display: flex;
    justify-content: center;
    margin: 23px 0 0 0;
}

.submitButton {
    display: block;
    width: 320px;
    max-width: 100%;
    padding: 10px 20px;
    margin: 0;
    border: none;
    outline: none;
    box-shadow: none;
    appearance: none;
    text-align: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: rgba(0,0,0,.3);
    background-color: #F2F2F2;
    pointer-events: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color .3s ease, color .3s ease, box-shadow .3s ease, transform .3s ease;

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 10px 10px -6px rgba(0, 0, 0, .24);
    }

    &:active {
        transform: translateY(-2px) scale(.95);
        box-shadow: none;
        transition: background-color .3s ease, color .3s ease, box-shadow .1s ease, transform .1s ease;
    }

    &.active {
        pointer-events: auto;
        background-color: #007AFF;
        color: #fff;
    }

    &.isFetching {
        pointer-events: none;
    }

    @media all and (max-width: $mobile-breakpoint) {
        font-size: 16px;
        line-height: 18px;
    }
}

.error {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    margin: 4px 0 0 0;
    padding: 0 0 0 10px;
    color: #DF4D40;
}

.spinner {
    padding: 0;
    display: block;
    margin: 0 auto;
}

.select {
    border: 1px solid #dadbdc;
}
